import { Mesh } from "three";
import { useLoader } from "@react-three/fiber";
import { SVGLoader } from "three/examples/jsm/loaders/SVGLoader.js";
import { useCallback, useEffect, useMemo, useState } from "react";
import fontData from './Montserrat_Regular.typeface.json'
import type { IExtMesh, IMeshValues, MeshFloorsProps } from "../../interfaces";
import { textLogoNamePrefix } from "../../defaults";
import { FontData, useFont } from "@react-three/drei";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { MapObjData } from "src/interfaces/mapApiTypes";
import { SVGResult } from "three-stdlib";
import useLoadFloors from "./useLoadFloors";
import useDrawLogoHelper from "./useDrawLogoHelper";

let allNonIndexedMapObjects: MapObjData[] = [];

const useMeshFloors = (): MeshFloorsProps => {
  const { mapConfig, initialFloorsDataIsLoaded, allIndexedMapObjects, allIndexedRetailers, allMapObjects, allNodesFloor, pathFinderGraph, setPathFinderGraph, mapApiResponse } = useMapBoxContext();

  const [meshParams, setMeshParams] = useState<IMeshValues[][]>([]);
  const [textParams, setTextParams] = useState<Array<{ textMesh: IExtMesh }[]>>([]);
  const [storeLogos, setStoreLogos] = useState<{ storeLogo: Mesh }[][]>([]);
  const [escalatorNodes, setEscalatorNodes] = useState<string[]>([]);

  let loadedFloors: SVGResult[] = useLoader(SVGLoader, mapApiResponse.floors.map(floor => floor.svgUrl)) as SVGResult[];
  const { GeometriesAndMaterials, graph, escalator_nodes, allSvgObjectIds } = useLoadFloors(loadedFloors);

  const { get_store_name_logo_geometry } = useDrawLogoHelper();

  const myFont = useFont(fontData as unknown as FontData);

  const handleAsyncStoreLogos = useCallback((floorIndex: number) => ((meshLogo: { storeLogo: Mesh }) => {
    setStoreLogos((prevLogos) => {
      if (prevLogos[floorIndex]) {
        prevLogos[floorIndex].push({ ...meshLogo });
      } else {
        prevLogos[floorIndex] = [{ ...meshLogo }];
      }
      return [...prevLogos];
    });
  }), [])

  const clearScene = useCallback(() => {
    setMeshParams((meshParams) => {
      if (meshParams) {
        meshParams.forEach(floor => {
          if (floor) {
            floor.forEach(value => {
              value.mesh.removeFromParent();
            })
          }
        })
      }
      return []
    });
    setTextParams((textParams) => {
      if (textParams) {
        textParams.forEach(floor => {
          if (floor) {
            floor.forEach(value => {
              value.textMesh.removeFromParent();
            })
          }
        })
      }
      return []
    });
    setStoreLogos(storeLogos => {
      if (storeLogos) {
        storeLogos.forEach(floor => {
          if (floor) {
            floor.forEach(value => {
              value.storeLogo.removeFromParent();
            })
          }
        })
      }
      return []
    });
  }, [])

  useEffect(() => {
    if (initialFloorsDataIsLoaded && mapConfig.ROLE === 'PORTAL') {
      return () => {
        // triggered when we change the apiServices (apiBaseUrl, mapConfig.CENTER_ID)
        clearScene();
      }
    }
  }, [clearScene, initialFloorsDataIsLoaded])

  useEffect(() => {
    return () => {
      clearScene();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (!myFont || !loadedFloors) return;

    const TextsAndLogos: Array<{ textMesh: IExtMesh }[]> = [];
    allNonIndexedMapObjects.forEach((params) => {
      let values: IMeshValues | undefined;
      for (let i = 0; i < GeometriesAndMaterials.length; i++) {
        //@ts-ignore
        values = GeometriesAndMaterials[i].find(item => item.object_id === params.map_obj_name);
        if (values !== undefined) {
          break;
        }
      }
      if (!values) return null;
      const textParams = get_store_name_logo_geometry(values.geometry, params.map_obj_name, values.floor_index, textLogoNamePrefix, myFont, handleAsyncStoreLogos(values.floor_index));
      if (textParams) {
        if (!TextsAndLogos[values.floor_index]) {
          TextsAndLogos[values.floor_index] = [];
        }
        TextsAndLogos[values.floor_index].push(textParams);
      }
    });

    const GAMCount = GeometriesAndMaterials.reduce((acc: number, item: IMeshValues[]) => {
      return (acc + item.length);
    }, 0);
    const countNonIndexedObjs = GAMCount - allNonIndexedMapObjects.length;
    if (countNonIndexedObjs !== 0) {
      const mapObjNames = new Set<string>();
      allNonIndexedMapObjects.forEach((item) => {
        mapObjNames.add(item.map_obj_name);
      });
      GeometriesAndMaterials.forEach((items: IMeshValues[]) => {
        items.forEach(item => {
          if (!mapObjNames.has(item.object_id)) {
            const textParams = get_store_name_logo_geometry(
              item.geometry,
              item.object_id,
              item.floor_index,
              textLogoNamePrefix,
              myFont,
              handleAsyncStoreLogos(item.floor_index)
            );
            if (textParams) {
              if (!TextsAndLogos[item.floor_index]) {
                TextsAndLogos[item.floor_index] = [];
              }
              TextsAndLogos[item.floor_index].push(textParams);
            }
          }
        })
      });
    }
    setMeshParams([...GeometriesAndMaterials]);
    setTextParams(TextsAndLogos);
    setPathFinderGraph(graph);
    setEscalatorNodes([...escalator_nodes]);

  }, [loadedFloors, myFont, allIndexedMapObjects, allIndexedRetailers, allMapObjects, allNodesFloor, pathFinderGraph, mapConfig, setPathFinderGraph, handleAsyncStoreLogos, GeometriesAndMaterials, get_store_name_logo_geometry, graph, escalator_nodes]);

  return useMemo(() => {
    return {
      meshParams: meshParams,
      textParams,
      storeLogos,
      pathFinderGraph,
      escalator_nodes: escalatorNodes,
      allSvgObjectIds
    }
  }, [meshParams, textParams, storeLogos, pathFinderGraph, escalatorNodes, allSvgObjectIds])
}

export default useMeshFloors;