import { IExtMesh, MapDeviceType, MeshFloorsProps, ZoomData } from "../../interfaces";
import { Map } from "./Map";
import { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from "react";
import { Group, Mesh, PerspectiveCamera, Vector3 } from "three";
import { ThreeEvent, useFrame, useThree } from "@react-three/fiber";
import { get_camera_focus_object } from "../../helpers/camera.helper";
import { MapControls } from "@react-three/drei";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { mapConfigDefaultVars } from "src/defaults";
import { MapCameraControlsState } from "src/interfaces/mapApiTypes";
import useRouteHelper from "src/Hooks/useMeshFloors/useRouteHelper";
import { AppFloor } from "src/interfaces/mapbox";

interface FloorsMapProps {
  meshFloors: MeshFloorsProps;
  currentFloorIndex: number;
  onPointerEnter?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerLeave?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerMove?: (e: ThreeEvent<PointerEvent>) => void;
  onClick?: (e: ThreeEvent<MouseEvent>) => void;
  currKioskObj: Mesh | null;
  currKioskFloorIndex: number;
  routeTargetId?: string;
  routeTargetFromId?: string;
  selectedAmenityType: string;
  activeObjectId?: string;
  hoverObjectId?: string;
  handleChangeFloor: (index: number) => void;
  escalatorNodes: string[];
  zoom: ZoomData | null;
  handleCameraLength?: (length: number) => void;
  cameraControlsPosition: MapCameraControlsState;
  handleCameraAndControlsChange?: ({ camera, controls }: MapCameraControlsState) => void;
  isSelectedRetailerSlug: boolean;
  setIsCameraInit: Function;
  handleObjectClick: (object: IExtMesh, targetId: string, softClick?: boolean) => void,
  triggerClick: boolean,
  deviceType: MapDeviceType;
}


export const FloorsMap = forwardRef((params: FloorsMapProps, ref) => {
  const { mapConfig, allMapObjects, allIndexedMapObjects, allNodesFloor, mapApiResponse, pathFinderGraph, setCameraLength, floorsData } = useMapBoxContext();
  const { make_amenity_route, create_route } = useRouteHelper();

  const [firstMove, setFirstMove] = useState<boolean>(false);
  const {
    meshFloors,
    currentFloorIndex,
    currKioskObj,
    currKioskFloorIndex,
    routeTargetId,
    routeTargetFromId,
    selectedAmenityType,
    handleChangeFloor,
    escalatorNodes,
    zoom,
    handleCameraLength,
    handleCameraAndControlsChange,
    activeObjectId,
    isSelectedRetailerSlug,
    setIsCameraInit,
    handleObjectClick,
    triggerClick,
    cameraControlsPosition,
    deviceType
  } = params;
  const [cameraLimits, setCameraLimits] = useState({ min: mapConfig.CAMERA.minDistance, max: mapConfig.CAMERA.maxDistance, initialDistance: 0, needsUpdate: true })

  const groupRef = useRef<Group>(null);
  const [routeTubes, setRouteTubes] = useState<Mesh[]>([]);
  const mapControlRef = useRef<any>(null);
  const { camera, scene } = useThree();
  const cameraFocus = useRef<{
    position: Vector3, target: Vector3,
    fromPosition: Vector3, fromTarget: Vector3,
    animationStartTime: number, duration: number
  } | null>(null);
  // const cameraFocus = useRef<{ position:Vector3, target:Vector3, animationStartTime:number, cameraTime:number, targetTime:number } | null>(null);

  const style = mapConfig.STYLE;
  const accentColor = mapConfig.ACCENT_COLOR;
  const from = currKioskObj ? (currKioskObj as IExtMesh).object_id :
    routeTargetFromId ? routeTargetFromId : undefined;
  const isUseDefaultsLimits = mapConfig.ROLE === "PORTAL" && routeTargetId;

  const resetCamera = useCallback((fullReset: boolean = false) => {
    if (!(camera instanceof PerspectiveCamera)) {
      console.error('Camera is not a PerspectiveCamera');
      return;
    }
    const selectedFloorMeshParams = meshFloors.meshParams[currKioskFloorIndex];
    if (!selectedFloorMeshParams?.length) {
      console.warn('objects not loaded yet');
      return;
    }

    let meshes: Mesh[];
    meshes = selectedFloorMeshParams.map(item => item.mesh);


    const focus = get_camera_focus_object(meshes, camera.fov, camera.aspect, mapConfig.ROLE === 'PORTAL' ? '2D' : style);

    let isZeroCCS = true;

    if (cameraControlsPosition) {
      let cameraV3 = cameraControlsPosition.camera;
      if (!(cameraV3 instanceof Vector3)) {
        cameraV3 = new Vector3(cameraV3.x, cameraV3.y, cameraV3.z);
      }
      isZeroCCS = (cameraV3 as Vector3).length() === 0;
    }

    if (!fullReset && cameraControlsPosition && !isZeroCCS && !['PORTAL'].includes(mapConfig.ROLE)) {
      focus.position.set(cameraControlsPosition.camera.x, cameraControlsPosition.camera.y, cameraControlsPosition.camera.z);
      focus.target.set(cameraControlsPosition.controls.x, cameraControlsPosition.controls.y, cameraControlsPosition.controls.z);
    }

    const cameraDistance = Math.min(
      cameraLimits.max,
      Math.max(
        cameraLimits.min,
        focus.position.distanceTo(focus.target)
      )
    );
    focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);

    cameraFocus.current = {
      ...focus,
      fromPosition: mapControlRef.current.object.position.clone(),
      fromTarget: mapControlRef.current.target.clone(),
      animationStartTime: performance.now(),
      duration: mapConfig.CAMERA.animSpeed, // how long it takes to move camera to get to new position
    };
    if (['DISPLAY_APP', 'WP_SITE'].includes(mapConfig.ROLE) && cameraLimits.needsUpdate === false && cameraLimits.initialDistance > 0) {
      setCameraLimits(cl => ({
        ...cl,
        min: mapConfig.CAMERA.minDistance < 0.1 ? 0.1 : mapConfig.CAMERA.minDistance,
        max: mapConfig.CAMERA.maxDistance,
      }));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [camera, cameraLimits.needsUpdate, mapConfig.CAMERA.animSpeed, mapConfig.CAMERA.maxDistance, mapConfig.CAMERA.minDistance, cameraControlsPosition, mapConfig.ROLE, currKioskFloorIndex, meshFloors.meshParams, style]);

  const focusDistance = cameraFocus.current ? cameraFocus.current.position.distanceTo(cameraFocus.current.target) : 0;

  useEffect(() => {
    if (cameraControlsPosition) {
      resetCamera()
    }
  }, [cameraControlsPosition, resetCamera])

  useEffect(() => {

    if (focusDistance > 0 && cameraLimits.needsUpdate) {
      setCameraLimits({ min: mapConfig.CAMERA.minDistance < 0.1 ? 0.1 : mapConfig.CAMERA.minDistance, max: mapConfig.CAMERA.maxDistance, initialDistance: focusDistance, needsUpdate: false })
      setIsCameraInit(true);
    }

  }, [focusDistance, cameraLimits.needsUpdate, mapConfig.CAMERA, setIsCameraInit])

  useEffect(() => {
    setCameraLimits((cl) => ({ ...cl, needsUpdate: true }))
  }, [mapConfig.CAMERA])

  // useEffect(() => {

  //   if (['DISPLAY_APP', 'WP_SITE'].includes(mapConfig.ROLE) && routeTargetId && cameraLimits.initialDistance > 0) {
  //     console.log('will set camera limit on routeTargetId')
  //     setCameraLimits(cl => {
  //       const minDistance = Math.max(Number(mapConfig.SELECTED_ZOOM_LIMIT), mapConfig.CAMERA.minDistance)
  //       console.log('mapConfig.CAMERA.minDistance', mapConfig.CAMERA.minDistance)
  //       console.log('mapConfig.SELECTED_ZOOM_LIMIT', mapConfig.SELECTED_ZOOM_LIMIT)
  //       console.log('set min: minDistance < 0.1 ? 0.1 : minDistance', minDistance < 0.1 ? 0.1 : minDistance)
  //       return {
  //         ...cl,
  //         needsUpdate: false,
  //         min: minDistance < 0.1 ? 0.1 : minDistance,
  //         max: mapConfig.CAMERA.maxDistance,
  //       }
  //     });
  //   }
  // }, [mapConfig.SELECTED_ZOOM_LIMIT, routeTargetId, cameraLimits.initialDistance, mapConfig.ROLE, mapConfig.CAMERA.minDistance, mapConfig.CAMERA.maxDistance])


  useEffect(() => {
    if (meshFloors.meshParams.length > 0) {
      resetCamera();
    }
  }, [meshFloors.meshParams.length, resetCamera])

  useEffect(() => {
    if (!selectedAmenityType || mapConfig.ROLE === 'PORTAL') {
      setRouteTubes([]);
      return;
    }
    const { routePaths, fromFloor } = make_amenity_route(from ?? '', selectedAmenityType, scene, escalatorNodes, style);
    setRouteTubes(routePaths);
    if (fromFloor !== undefined) {
      handleChangeFloor(fromFloor)
    }

    if (currKioskObj && mapConfig.ROLE === "DISPLAY_APP") {
      handleChangeFloor(currKioskFloorIndex);
    }

  }, [selectedAmenityType, scene, pathFinderGraph, floorsData, escalatorNodes, style, from, mapConfig.ROLE, allNodesFloor, allIndexedMapObjects, make_amenity_route, currKioskObj, handleChangeFloor, currKioskFloorIndex])

  const clearEscalators = useCallback(() => {
    floorsData.flat().forEach((item) => item.escalatorMeshes = []);
  }, [floorsData])

  useEffect(() => {

    if (!from || !routeTargetId || mapConfig.ROLE === 'PORTAL') {
      if (mapConfig.ROLE === 'PORTAL') {
        if (routeTargetId) {
          const targetMesh = scene.getObjectByProperty('object_id', routeTargetId);
          if (targetMesh) {
            setRouteTubes([targetMesh as IExtMesh])
          }
        } else {
          clearEscalators();
          setRouteTubes([]);
        }
      } else if (!selectedAmenityType) {
        clearEscalators();
        setRouteTubes([]);
      }
      return;
    }
    if (!selectedAmenityType) {
      floorsData.forEach(floor => {
        floor.escalatorMeshes = [];
      });
    }

    // console.log('Make route from %s to %s', from, routeTargetId, pathFinderGraph);

    /* MAKE ROUTE PATH */

    const { routePaths, fromFloor } = create_route(allNodesFloor, from, routeTargetId, scene, floorsData, meshFloors.escalator_nodes, pathFinderGraph, style);

    setRouteTubes(routePaths);
    if (fromFloor !== undefined) {
      handleChangeFloor(fromFloor)
    }

  }, [from, routeTargetId, scene, camera, floorsData, accentColor, pathFinderGraph, style, meshFloors.escalator_nodes, selectedAmenityType, mapConfig.ROLE, allNodesFloor, triggerClick, create_route, clearEscalators, handleChangeFloor]);

  const selectedFloorMeshParams = meshFloors.meshParams[currentFloorIndex];
  useEffect(() => {
    if (mapConfig.ROLE === 'PORTAL_RESPONSIVE') return;

    if (!(camera instanceof PerspectiveCamera)) {
      console.error('Camera is not a PerspectiveCamera');
      return;
    }
    if (!selectedFloorMeshParams?.length) {
      console.warn('objects not loaded yet');
      return;
    }

    let routeMeshes = floorsData[currentFloorIndex]?.routeMeshes

    if (!activeObjectId && !selectedAmenityType) {
      return;
    }

    let meshes: Mesh[] = [];
    if (routeTubes[currentFloorIndex] && routeMeshes?.length) {

      meshes = [...routeMeshes]
    } else if (activeObjectId && (mapConfig.ROLE === 'PORTAL' || isSelectedRetailerSlug)) {
      const currentActiveObject = routeTargetId && selectedFloorMeshParams.find(item => item.mesh.object_id === routeTargetId);
      if (currentActiveObject) {
        meshes = [currentActiveObject.mesh];
      }
    }
    if (!meshes.length) {
      meshes = selectedFloorMeshParams.map(item => item.mesh);
    }

    const focus = get_camera_focus_object(meshes, camera.fov, camera.aspect, mapConfig.ROLE === 'PORTAL' || mapConfig.ROLE === 'WP_SITE' ? '2D' : style);
    const cameraDistance = Math.min(
      isUseDefaultsLimits ? mapConfig.CAMERA.maxDistance : cameraLimits.max,
      Math.max(
        isUseDefaultsLimits ? mapConfig.CAMERA.minDistance : cameraLimits.min,
        focus.position.distanceTo(focus.target)
      )
    );
    focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);
    cameraFocus.current = {
      ...focus,
      fromPosition: mapControlRef.current.object.position.clone(),
      fromTarget: mapControlRef.current.target.clone(),
      animationStartTime: performance.now(),
      duration: mapConfig.CAMERA.animSpeed, // how long it takes to move camera to get to new position
    };

    // return () => {
    //     cameraFocus.current = null;
    // };
  }, [routeTubes, selectedFloorMeshParams, currentFloorIndex, style, camera, routeTargetId, mapConfig.ROLE, mapConfig.CAMERA.maxDistance, mapConfig.CAMERA.minDistance, mapConfig.CAMERA.animSpeed, floorsData, activeObjectId, selectedAmenityType, isSelectedRetailerSlug, isUseDefaultsLimits, cameraLimits.max, cameraLimits.min]);

  useEffect(() => {
    if (zoom?.direction && mapControlRef.current) {
      if (!(camera instanceof PerspectiveCamera)) {
        console.error('Camera is not a PerspectiveCamera');
        return;
      }

      if (!selectedFloorMeshParams.length) {
        console.warn('objects not loaded yet');
        return;
      }

      let meshes: Mesh[];
      if (routeTubes[currentFloorIndex]) {
        meshes = [routeTubes[currentFloorIndex]];

        const currentActiveObject = routeTargetId && selectedFloorMeshParams.find(item => item.mesh.object_id === routeTargetId);
        if (currentActiveObject) {
          meshes.push(currentActiveObject.mesh);
        }
      } else {
        meshes = selectedFloorMeshParams.map(item => item.mesh);
      }
      const focus = {
        position: mapControlRef.current.object.position.clone(),
        target: mapControlRef.current.target.clone()
      };

      let cameraDistance: number = parseInt(focus.position.distanceTo(focus.target));

      if (zoom.direction === 'in') {
        cameraDistance -= mapConfig.ZOOM_STEP;
      } else {
        cameraDistance += mapConfig.ZOOM_STEP;
      }

      cameraDistance = Math.min(
        cameraLimits.max,
        Math.max(
          cameraLimits.min,
          cameraDistance
        )
      );

      focus.position.sub(focus.target).setLength(cameraDistance).add(focus.target);

      cameraFocus.current = {
        ...focus,
        fromPosition: mapControlRef.current.object.position.clone(),
        fromTarget: mapControlRef.current.target.clone(),
        animationStartTime: performance.now(),
        duration: mapConfig.CAMERA.animSpeed, // how long it takes to move camera to get to new position
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [zoom])

  const getInitialDistance = () => {
    return cameraLimits.initialDistance
  }

  const onCameraMove = useCallback((e: any) => {
    if (handleCameraLength) {
      const vector = new Vector3();
      vector.copy(e.target.object.position);
      vector.sub(mapControlRef.current.target);
      handleCameraLength(vector.length());
      setCameraLength(vector.length());
      if (handleCameraAndControlsChange) {
        // todo this is an expensive operation that shouldn't be needed
        handleCameraAndControlsChange({
          camera: mapControlRef.current.object.position.clone(),
          controls: mapControlRef.current.target.clone()
        })
      }
    }
  }, [handleCameraLength, handleCameraAndControlsChange, setCameraLength])

  useFrame(() => {
    if (cameraFocus.current?.duration && cameraFocus.current?.duration > 0) {
      if (!firstMove) {
        setFirstMove(true);
        cameraFocus.current.duration = 0;
      }
      const focus = cameraFocus.current;
      const animationTime = (performance.now() - focus.animationStartTime) / 1000;

      const animationProgress = Math.min(1, animationTime / focus?.duration);
      mapControlRef.current.object.position.lerpVectors(focus.fromPosition, focus.position, animationProgress);
      mapControlRef.current.target.lerpVectors(focus?.fromTarget, focus.target, animationProgress);

      mapControlRef.current.object.updateProjectionMatrix();
      if (animationProgress >= 1) {
        cameraFocus.current = null;
      }
    }
  })

  const createRouteToStore = useCallback((retailerId: number) => {
    if (retailerId > 0) {
      const retailer = mapApiResponse.retailers.find(item => item.id === retailerId);
      if (retailer) {
        const mapObjs = mapApiResponse.map_objs.filter(item => item.retailer_id === retailer.id);
        for (let i = 0; i < mapObjs.length; i++) {
          if (allMapObjects.includes(mapObjs[i].map_obj_name)) {
            const obj = scene.getObjectByProperty('object_id', mapObjs[i].map_obj_name);
            if (obj) {
              handleObjectClick(obj as IExtMesh, mapObjs[i].map_obj_name, true);
            }
            break;
          }
        }
      }
    }
  }, [handleObjectClick, mapApiResponse, scene]);

  useImperativeHandle(ref, () => ({
    resetCamera,
    createRouteToStore,
    getInitialDistance,
  }))


  return (<group rotation={[Math.PI / 2, 0, 0]} ref={groupRef}>
    <>
      <MapControls
        makeDefault
        onChange={onCameraMove}
        ref={mapControlRef}
        maxPolarAngle={Math.PI / 2}
        minDistance={isUseDefaultsLimits ? mapConfigDefaultVars.CAMERA.minDistance : cameraLimits.min}
        maxDistance={isUseDefaultsLimits ? mapConfigDefaultVars.CAMERA.maxDistance : cameraLimits.max}
        enableZoom={mapConfig.ROLE === "PORTAL" || mapConfig.ROLE === "DISPLAY_APP" || (mapConfig.ROLE === "WP_SITE" && ['mobile', 'tablet'].includes(deviceType))}
        enableRotate={mapConfig.ROLE !== "WP_SITE" && mapConfig.ROLE !== "PORTAL_RESPONSIVE" && mapConfig.ROLE !== "DISPLAY_APP"}
        dampingFactor={0.3}
      />

      {floorsData && floorsData.map((value: AppFloor, index: number) => (
        <Map
          key={`Map-${index}`}
          visible={currentFloorIndex === index}
          floorIndex={index}
          currentFloorIndex={currentFloorIndex}
          currKioskObj={params.currKioskObj}
          activeObjectId={params.activeObjectId}
          hoverObjectId={params.hoverObjectId}
          meshFloors={meshFloors}
          routeTube={mapConfig.ROLE !== 'PORTAL' ? routeTubes && routeTubes[index] : undefined}
          onPointerEnter={params.onPointerEnter}
          onPointerLeave={params.onPointerLeave}
          onPointerMove={params.onPointerMove}
          onClick={params.onClick}
          handleChangeFloor={handleChangeFloor}
        />
      ))}
    </>
  </group>)
})