import axios from "axios";
import createMapApiService, { MapApiService } from "./map.service";
import createRetailersService, { RetailersService } from "./retailers.service";

export interface ApiServicesProps {
  mapApiService: MapApiService;
  retailersService: RetailersService;
}

const createApiServices = (baseApiUrl: string, center_id: number): ApiServicesProps => {
  const axiosInstance = axios.create({
    baseURL: baseApiUrl, // server baseURL
    timeout: 30000, // Set the request timeout in milliseconds
    headers: {
      "Content-Type": "application/json",
      "center_id": center_id
    },
  });

  // Interceptor for handling errors for Axios instance
  const errorInterceptor = (error: any) => {
    console.error('Axios Interceptor Error: ', error.response);
  };

  axiosInstance.interceptors.response.use(
    (response) => response,
    errorInterceptor
  );


  const mapApiService: MapApiService = createMapApiService(axiosInstance);
  const retailersService: RetailersService = createRetailersService(axiosInstance);

  return {
    mapApiService,
    retailersService
  }
}

export default createApiServices;

