import React, { CSSProperties, forwardRef, useCallback, useEffect, useImperativeHandle, useMemo, useRef, useState, useTransition } from "react";
import { Canvas, ThreeEvent } from "@react-three/fiber";
import useMeshFloors from "../../Hooks/useMeshFloors";
import { SceneProperties } from "./SceneProperties";
import { CameraProperties } from "./CameraProperties";
import { Mesh, Object3D } from "three";
import { IExtMesh, MeshFloorsProps, IMeshValues, ZoomData, MapDeviceType } from "src/interfaces";
import { FloorsMap } from "./FloorsMap";
import { Stats } from '@react-three/drei';
import UIComponent from "../UIComponent";
import styles from './SceneComponent.module.scss';
import RetailerInfoModal from "./retailer-info/RetailerInfoModal";
import { AllAmenities, MapAmenityID } from "src/helpers/amenities.helper";
import { cameraControlsDefaultVars, textLogoNamePrefix } from "src/defaults";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { MapCameraControlsState, MapObjData } from "src/interfaces/mapApiTypes";
import PortalSidebarForm from "./portal-sidebar-form/PortalSidebarForm";

interface SceneComponentProps {
  selectedActiveObjectId: string;
  setSelectedActiveObjectId: React.Dispatch<React.SetStateAction<string>>;
  onMapDataUpdate?: (data: MapObjData[]) => void;
  onSubmit?: (data: MapObjData, refreshData?: () => void) => void;
  onResetData?: (data: MapObjData) => void;
  onChangeData?: (data: MapObjData) => void;
  onNewData?: (data: MapObjData) => void;
  onChangeCameraControlPosition?: (cameraControlPos: any) => void;
  onExtractedAmenities?: (amenityIds: MapAmenityID[]) => void;
  handleClickOnRetailer?: (retailer_id: number) => void;
}

export type TFormMapObjData = {
  index: number,
  data: MapObjData,
}

const SceneComponent = forwardRef((params: SceneComponentProps, ref) => {
  const { selectedActiveObjectId, setSelectedActiveObjectId, onMapDataUpdate, onChangeCameraControlPosition, handleClickOnRetailer } = params;

  const [triggerClick, setTriggerClick] = useState<boolean>(false);
  const [fromObjectId, setFromObjectId] = useState<string>('');
  const [hovered, setHovered] = useState<boolean>(false);
  const [selectedFloorIndex, setSelectedFloorIndex] = useState<number>(-1);
  const [formMapObjData, setFormMapObjData] = useState<TFormMapObjData[]>([]);
  const [cameraControlsPosition, setCameraControlsPosition] = useState<MapCameraControlsState>(cameraControlsDefaultVars);
  const [canvasWidth, setCanvasWidth] = useState<number | undefined>(0);
  const [deviceType, setDeviceType] = useState<MapDeviceType>('desktop');
  const [IsCameraInit, setIsCameraInit] = useState(false);
  const [refreshState, setRefreshState] = useState<boolean>(false);
  const [, startTransition] = useTransition();

  const [selectedRetailerId, setSelectedRetailerId] = useState<number>(0);

  const [selectedAmenityType, setSelectedAmenityType] = useState<string>('');
  const [selectedAmenityMapObjName, setSelectedAmenityMapObjName] = useState<string>('');
  const [zoom, setZoom] = useState<ZoomData | null>(null);

  const [, setCameraLength] = useState<number>(0);

  const statsParentRef = useRef<HTMLDivElement | null>(null);
  const canvasWidthRef = useRef<number | undefined | null>(null);
  canvasWidthRef.current = canvasWidth;

  const floorsMapRef = useRef<{
    resetCamera: (fullReset?: boolean, cameraControlsStates?: any) => void | null,
    createRouteToStore: (retailerId: number) => void | null,
    getInitialDistance: () => number,
  }>(null);

  const handleCameraLength = useCallback((length: number) => {
    setCameraLength(length);
  }, [])

  const { setMapConfig, refetchMapData, mapApiResponse, mapConfig, floorsData, setMeshObject, indexedKiosks, initialFloorsDataIsLoaded } = useMapBoxContext();

  const handleFloorChange = useCallback((floorIndex: number) => {
    setSelectedFloorIndex(floorIndex);
  }, [setSelectedFloorIndex])

  const meshFloors = useMeshFloors();
  const { meshParams, storeLogos, textParams, allSvgObjectIds } = meshFloors;

  const labelRef = useRef<HTMLDivElement>(null);
  const [currentHoveredObject, setCurrentHoveredObject] = useState<Object3D | null>(null);
  const [, setSceneKey] = useState(0);

  const updateKey = useCallback(() => {
    setSceneKey((value) => value + 1)
  }, [])

  const [showRetailerInfo, setShowRetailerInfo] = useState<boolean>(false);

  useImperativeHandle(ref, () => ({
    refetchMapData,
    createRouteToAmenity: handleAmenityClick,
    createRouteToStore: floorsMapRef.current?.createRouteToStore,
    getInitialDistance: floorsMapRef.current?.getInitialDistance,
    resetMap: resetHandle,
    setMapConfig: setMapConfig,
    refreshState: setRefreshState
  }))

  const setDeviceTypeCallback = useCallback((newCanvasWidth: number) => {
    if (mapConfig.ROLE === 'DISPLAY_APP') {
      setDeviceType('display_app');
    } else if (mapConfig.ROLE === "PORTAL_RESPONSIVE") {
      setDeviceType(mapConfig.DEVICE);
    } else {
      if (newCanvasWidth > 1024) {
        setDeviceType('desktop');
      } else if (newCanvasWidth > 767) {
        setDeviceType('tablet');
      } else {
        setDeviceType('mobile');
      }
    }
  }, [mapConfig])

  useEffect(() => {
    if (statsParentRef.current) {
      const newCanvasWidth = statsParentRef.current.offsetWidth
      setCanvasWidth(newCanvasWidth);
      setDeviceTypeCallback(newCanvasWidth);
      const resizeWidth = () => {
        if (statsParentRef.current) {
          const newCanvasWidth = statsParentRef.current.offsetWidth
          setCanvasWidth(newCanvasWidth);
          setDeviceTypeCallback(newCanvasWidth);
        }
      }
      window.addEventListener('resize', resizeWidth);
      resizeWidth();
      return () => {
        window.removeEventListener('resize', resizeWidth);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statsParentRef.current, refreshState, setDeviceTypeCallback]);

  useEffect(() => {
    if (mapConfig.ROLE === "DISPLAY_APP") {
      setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.display_app);
    } else if (mapConfig.ROLE === "WP_SITE") {
      if (deviceType === 'desktop') {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.desktop);
      } else if (deviceType === 'tablet') {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.tablet);
      } else {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.mobile);
      }
    } else if (mapConfig.ROLE === "PORTAL_RESPONSIVE") {
      if (mapConfig.DEVICE === "desktop") {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.desktop);
      } else if (mapConfig.DEVICE === "display_app") {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.display_app);
      } else if (mapConfig.DEVICE === "mobile") {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.mobile);
      } else if (mapConfig.DEVICE === "tablet") {
        setCameraControlsPosition(mapConfig.CAMERA_CONTROLS_STATES.tablet);
      }
    }
  }, [deviceType, mapConfig])

  const handleCameraAndControlsChange = useCallback((cameraControlsState: MapCameraControlsState) => {
    onChangeCameraControlPosition && onChangeCameraControlPosition(cameraControlsState);
    //this state is unused, it's an expensive update, commenting it out
    // setCameraControlsPosition(cameraControlsState);
  }, [onChangeCameraControlPosition])

  // if selectedActiveObjectId is not -1, then it is set to the DEFAULT_SELECTED_STORE
  let activeFromObjectId = '';
  let activeObjectId = mapConfig.DEFAULT_SELECTED_STORE ?? '';
  if (selectedActiveObjectId) {
    activeObjectId = selectedActiveObjectId;
    activeFromObjectId = fromObjectId;
  } else if (selectedAmenityType) {
    activeObjectId = '';
    activeFromObjectId = fromObjectId;
    // activeFromObjectId = '';
  }

  const handleChangeMapData = useCallback((index: number, newData: MapObjData) => {
    setFormMapObjData((prev) => {
      const itemIndex = prev.findIndex((item) => item.data.map_obj_name === newData.map_obj_name);

      if (itemIndex !== -1) {
        prev[itemIndex] = { index, data: { ...newData } };
      } else {
        prev.push({ index, data: { ...newData } })
      }

      return [...prev];
    });

  }, [])

  const handleOnSubmit = useCallback((data: MapObjData) => {
    if (params.onSubmit) {
      params.onSubmit(data, refetchMapData);
    }
  }, [params, refetchMapData])

  useEffect(() => {
    document.body.style.cursor = currentHoveredObject ? 'pointer' : 'default';
  }, [currentHoveredObject]);

  const [currKioskObj, currKioskFloorIndex, kioskError]: [Mesh | null, number, string] = useMemo(() => {
    let kioskError = '';
    let currKioskObj: Mesh | null = null;
    let currKioskFloorIndex = floorsData.length ? floorsData.length - 1 : 0;
    if (indexedKiosks[mapConfig.KIOSK_ID]) {
      const kioskMapObjName = indexedKiosks[mapConfig.KIOSK_ID].map_obj_name;
      const currKioskMeshParams = meshParams?.flat().find(mesh => mesh.object_id === kioskMapObjName);
      if (currKioskMeshParams) {
        currKioskObj = currKioskMeshParams.mesh;
        currKioskFloorIndex = currKioskMeshParams.floor_index;
      }
    } else {
      if (mapConfig.ROLE === 'DEMO') {
        // kioskError = "This Kiosk is not assigned in MAP, so routes won't work. Please assign the Kiosk from Edit Map first.";
        console.error('Kiosk not found in config');
        // kioskError = 'Map initialization error';
      }
    }

    return [currKioskObj, currKioskFloorIndex, kioskError]
  }, [mapConfig, meshParams, floorsData, indexedKiosks]);

  const currentFloorIndex = selectedFloorIndex > -1 && selectedFloorIndex < floorsData.length ? selectedFloorIndex : currKioskFloorIndex;

  const resetHandle = useCallback((fullReset: boolean = false) => {
    setSelectedActiveObjectId('');
    setFromObjectId('');
    setSelectedAmenityType('');
    setSelectedRetailerId(0);
    handleFloorChange(currKioskFloorIndex);

    if (floorsMapRef.current) {
      if (mapConfig.ROLE === "PORTAL_RESPONSIVE") {
        floorsMapRef?.current?.resetCamera(fullReset);
      } else {
        floorsMapRef?.current?.resetCamera(fullReset);
      }
    }
  }, [currKioskFloorIndex, handleFloorChange, mapConfig.ROLE, setSelectedActiveObjectId])

  useEffect(() => {
    const kiosks: IMeshValues[] = [];
    meshParams.forEach(floor => {
      floor.forEach(item => {
        if (item?.mesh_type === "kiosk") {
          kiosks.push(item);
        }
      })
    })

    const mapObjectIdToLogo = new Map();

    storeLogos.filter(item => item !== undefined).forEach(floor => {
      floor.forEach(item => {
        mapObjectIdToLogo.set(item.storeLogo.object_id, item.storeLogo);
      })
    });

    const currKioskId = (currKioskObj as IExtMesh)?.object_id;
    const currKioskLogo: IExtMesh[] = [];
    kiosks.forEach(item => {
      const kiosk = item.mesh;
      const logo = mapObjectIdToLogo.get('custom-layer-' + item.object_id);
      const mapObj = mapApiResponse.map_objs.find(item => item.map_obj_name === kiosk.object_id);
      if (kiosk && logo) {
        if (logo.userData.kioskConfig !== null) {
          logo.userData.kioskConfig = {
            lockSize: mapObj?.lock_size,
            blink: item.object_id === currKioskId,
            size: mapObj ? mapObj.size : mapConfig.KIOSK_SIZE,
            floorIndex: kiosk.floor_index,
          }
          // logo.userData.htmlContent = <MapCenterMarker lockSize={mapObj?.value === '1'} blink={true} size={mapObj ? Number(mapObj.size) : Number(mapConfig.KIOSK_SIZE)} />
          logo.userData.distanceFactor = mapObj?.lock_size ? 1500 : undefined;
          logo.visible = true;
        }
        currKioskLogo.push(logo);
      }
    });

  }, [storeLogos, meshParams, currKioskObj, mapConfig, mapApiResponse.map_objs]);


  useEffect(() => {
    if (onMapDataUpdate) {
      const exportData: MapObjData[] = [];

      formMapObjData.forEach((value) => {
        exportData.push({ ...value.data });
      })
    }
  }, [formMapObjData, onMapDataUpdate])

  useEffect(() => {
    document.body.style.cursor = hovered ? 'pointer' : 'auto';

  }, [hovered])

  useEffect(() => {
    if (mapConfig?.SELECTED_RETAILER_ID && IsCameraInit) {
      setSelectedRetailerId(mapConfig.SELECTED_RETAILER_ID);
    }
  }, [mapConfig, IsCameraInit])

  useEffect(() => {
    if (mapConfig && selectedRetailerId > 0) {
      const retailer = mapApiResponse.retailers.find(item => {
        return item.id === selectedRetailerId;
      })
      if (retailer) {
        const mapObject = mapApiResponse.map_objs.find(item => item.retailer_id === retailer.id);
        const meshParam = meshParams?.flat().find(item => item.object_id === mapObject?.map_obj_name);
        if (meshParam) {
          setSelectedFloorIndex(meshParam.floor_index);
        }
        if (mapObject) {
          setSelectedActiveObjectId(mapObject.map_obj_name);
        }
      }
    }
  }, [mapConfig, setSelectedActiveObjectId, selectedRetailerId, meshParams, mapApiResponse.map_objs, mapApiResponse.retailers])


  const handleMeshObjectContext = useCallback((object: IExtMesh) => {
    const singleTextParams = textParams[currentFloorIndex]?.find(value => {
      return value.textMesh.object_id === `${textLogoNamePrefix}${(object).object_id}`
    })

    const logoParams = storeLogos[currentFloorIndex]?.find(value => {
      return value.storeLogo.object_id === `${textLogoNamePrefix}${(object).object_id}`
    })

    const result = [object];

    if (singleTextParams) {
      result.push(singleTextParams.textMesh);
    }

    if (logoParams) {
      result.push(logoParams.storeLogo)
    }

    setMeshObject([...result]);
  }, [storeLogos, textParams, setMeshObject, currentFloorIndex])

  useEffect(() => {
    if (meshParams && meshParams.length && selectedActiveObjectId) {
      const object = meshParams[currentFloorIndex]?.find((value) => value.object_id === selectedActiveObjectId)?.mesh;
      if (object) {
        handleMeshObjectContext(object);
      }
    }
  }, [meshParams, handleMeshObjectContext, selectedActiveObjectId, currentFloorIndex])

  const handleAmenityClick = useCallback((type: string, map_obj_name?: string) => {
    setSelectedActiveObjectId('');
    setFromObjectId('')
    setSelectedAmenityType(type)
    if (map_obj_name) {
      setSelectedAmenityMapObjName(map_obj_name);
      if (mapConfig.ROLE === 'DISPLAY_APP') {
        setSelectedActiveObjectId(map_obj_name);
      }
    }
    if (mapConfig.ROLE === 'WP_SITE') {
      setShowRetailerInfo(true);
    }
  }, [mapConfig.ROLE, setSelectedActiveObjectId]);

  const handleObjectClick = useCallback((object: IExtMesh, targetId: string, softClick?: boolean) => {
    if (!softClick && mapConfig.ROLE === 'DISPLAY_APP' && handleClickOnRetailer && object.mesh_type?.startsWith("store")) {
      const mapObj = mapApiResponse.map_objs.find((item) => item.map_obj_name === object.object_id && item.obj_type === "retailer")
      if (mapObj) {
        const retailer = mapApiResponse.retailers.find((item) => item.id === mapObj.retailer_id)
        if (retailer) {
          handleClickOnRetailer(retailer.id)
        }
      }

      return false;
    }

    if (mapConfig.ROLE === 'DISPLAY_APP' && currKioskObj) {
      handleFloorChange(currKioskFloorIndex);
    }

    startTransition(() => {
      handleMeshObjectContext(object);
      setSelectedActiveObjectId(targetId ?? '');
      setTriggerClick(t => !t);
      setSelectedRetailerId(0);
      setSelectedAmenityType('');
      setFromObjectId('');
    })

  }, [currKioskFloorIndex, currKioskObj, handleClickOnRetailer, handleFloorChange, handleMeshObjectContext, mapApiResponse.map_objs, mapApiResponse.retailers, mapConfig.ROLE, setSelectedActiveObjectId])

  const onClick = useCallback((e: ThreeEvent<MouseEvent>) => {
    e.stopPropagation();
    if (e.delta > 15) { // dragged 15px before release
      return // likely a drag motion 
    }

    const targetId = (e.object as IExtMesh).object_id as string;
    const mapObj = mapApiResponse.map_objs.find(item => item.map_obj_name === targetId);
    if (mapObj && mapObj.layer_type === 'amenity') {
      handleAmenityClick(mapObj.value as MapAmenityID, targetId);
      if (mapConfig.ROLE === 'PORTAL') {
        handleMeshObjectContext(e.object as IExtMesh);
        setSelectedActiveObjectId(targetId ?? '');
      }
    } else {
      handleObjectClick(e.object as IExtMesh, targetId ?? '');
      if (mapConfig.ROLE !== 'PORTAL' && !mapConfig.KIOSK_ID && mapObj?.retailer_id) {
        setShowRetailerInfo(true);
      }
    }
  }, [mapConfig, handleAmenityClick, setSelectedActiveObjectId, handleMeshObjectContext, handleObjectClick, mapApiResponse.map_objs]);

  const onPointerEnter = useCallback((e: ThreeEvent<PointerEvent>) => {
    e.stopPropagation();

    setHovered(true);
    if (e.object.userData.storeName === '') {
      return;
    }
    setCurrentHoveredObject(e.object);
  }, []);

  const onPointerLeave = useCallback((e: ThreeEvent<PointerEvent>) => {
    e.stopPropagation();
    setCurrentHoveredObject(null);
    setHovered(false);
    if (labelRef && labelRef.current) {
      labelRef.current.style.display = 'none';
    }
  }, []);

  const startMousePos = useRef<{ x: number | null, y: number | null, distance: number }>({ x: null, y: null, distance: -Infinity });

  const onPointerMove = useCallback((e: ThreeEvent<PointerEvent>) => {
    e.stopPropagation();
    if (e.buttons > 0) {
      if (startMousePos.current.x === null) {
        startMousePos.current.x = e.clientX;
      }
      if (startMousePos.current.y === null) {
        startMousePos.current.y = e.clientY;
      }
      if (startMousePos.current.x !== null && startMousePos.current.y !== null) {
        let newDistance = Math.sqrt(Math.pow(startMousePos.current.x - e.clientX, 2) + Math.pow(startMousePos.current.y - e.clientY, 2));
        if (newDistance > startMousePos.current.distance) {
          startMousePos.current.distance = newDistance;
        }
      }
    } else {
      startMousePos.current.x = startMousePos.current.y = null;
      startMousePos.current.distance = -Infinity;
    }

    if (labelRef && labelRef.current && e.object.userData.storeName && e.object.userData.storeName !== '') {
      labelRef.current.style.top = `${e.offsetY - 65}px`;
      labelRef.current.style.left = `${e.offsetX}px`;
      labelRef.current.style.display = 'block';
    } else if (labelRef.current) {
      labelRef.current.style.display = 'none';
    }
  }, [])

  const messageStyle: CSSProperties = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    whiteSpace: 'nowrap'
  }

  const handleRetailerInfoOnClick = useCallback((map_obj_name: string, toMapObjName?: string) => {
    setFromObjectId(map_obj_name);
    if (toMapObjName) {
      setSelectedActiveObjectId(toMapObjName || '');
      // setSelectedAmenityType(amenityType);
    }
    setShowRetailerInfo(false);
  }, [setFromObjectId, setShowRetailerInfo, setSelectedActiveObjectId])

  if (!initialFloorsDataIsLoaded) {
    return <div className="messageStyle" style={messageStyle}>Map loading, please wait...</div>
  }

  if (floorsData.length <= 0) {
    return <div className="messageStyle" style={messageStyle}>Map doesn't have any floors</div>
  }

  if (!meshParams.length) {
    if (mapConfig.ROLE === 'WP_SITE') {
      return <div className="messageStyle" style={messageStyle}>No Map for Center</div>
    }
    return <div className="messageStyle" style={messageStyle}>Map loading, please wait...</div>
  }

  if (kioskError) {
    return <div className="messageStyle" style={messageStyle}>{kioskError}</div>
  }

  if (!currKioskObj && mapConfig.ROLE === 'DEMO') {
    console.error('NO KIOSK OBJECT FOUND');
  }

  // make sure that meshFloors conditionals are not null
  const meshFloorsChecked: MeshFloorsProps = meshFloors as MeshFloorsProps;

  let hoverObject = currentHoveredObject as IExtMesh
  let hoverObjectId = '';
  if (hoverObject && hoverObject.object_id) {
    hoverObjectId = hoverObject.object_id;
  }

  return (
    <>
      {mapConfig.ROLE === 'PORTAL' &&
        <div className={`mapBoxSidebar ${styles.mapBoxSidebar} ${selectedActiveObjectId ? styles.active : ''}`}>
          <div className={`sidebarWrapper ${styles.sidebarWrapper}`}>
            <div className={`sidebarBox ${styles.sidebarBox}`}>
              {selectedActiveObjectId ? (
                <PortalSidebarForm
                  floorIndex={currentFloorIndex}
                  meshFloors={meshFloors}
                  setData={handleChangeMapData}
                  selectedId={selectedActiveObjectId}
                  onSubmit={handleOnSubmit}
                  onResetData={params.onResetData}
                  onChangeData={params.onChangeData}
                  onNewData={params.onNewData}
                  triggerSceneUpdate={updateKey}
                />
              ) : (
                null
                // <Alert variant="light">Select a store to customize.</Alert>
              )}
            </div>
          </div>
        </div>
      }

      <div ref={statsParentRef} className={`mapBox ${styles.mapBox}`}>
        <UIComponent
          selectedFloorIndex={currentFloorIndex}
          handleFloorChange={handleFloorChange}
          reset={resetHandle}
          zoomIn={() => setZoom({ direction: 'in' })}
          zoomOut={() => setZoom({ direction: 'out' })}
          amenitiesList={AllAmenities}
          handleAmenityClick={handleAmenityClick}
          onExtractedAmenities={params.onExtractedAmenities}
        />

        <Canvas style={{ position: 'absolute', top: 0, left: 0 }} flat>
          {Number(mapConfig.STATS) ? <Stats className={styles.stats} parent={statsParentRef} /> : null}

          <SceneProperties background={mapConfig.MAP_BACKGROUND_COLOR} />
          <CameraProperties near={1} far={mapConfig.CAMERA.far} fov={mapConfig.CAMERA.fov} />

          <ambientLight intensity={1} color={'#c4c4c4'} />
          <directionalLight position={[0, 400, 0]} color={'#ffffff'} intensity={0.3} />
          <directionalLight position={[0, 0, 400]} color={'#c4c4c4'} intensity={0.2} />
          <directionalLight position={[0, 0, -400]} color={'#c4c4c4'} intensity={0.2} />

          <FloorsMap
            meshFloors={meshFloorsChecked}
            activeObjectId={activeObjectId}
            hoverObjectId={hoverObjectId}
            currKioskObj={currKioskObj}
            currKioskFloorIndex={currKioskFloorIndex}
            routeTargetId={activeObjectId}
            routeTargetFromId={activeFromObjectId}
            currentFloorIndex={currentFloorIndex}
            onPointerEnter={onPointerEnter}
            onPointerLeave={onPointerLeave}
            onPointerMove={onPointerMove}
            onClick={onClick}
            handleChangeFloor={handleFloorChange}
            selectedAmenityType={selectedAmenityType}
            escalatorNodes={meshFloors.escalator_nodes}
            zoom={zoom}
            handleCameraLength={handleCameraLength}
            ref={floorsMapRef}
            cameraControlsPosition={cameraControlsPosition}
            handleCameraAndControlsChange={handleCameraAndControlsChange}
            isSelectedRetailerSlug={true}
            setIsCameraInit={setIsCameraInit}
            handleObjectClick={handleObjectClick}
            triggerClick={triggerClick}
            deviceType={deviceType}
          />
        </Canvas>

        {mapConfig.ROLE !== 'DISPLAY_APP' && (mapConfig.ROLE !== 'WP_SITE' || !['mobile', 'tablet'].includes(deviceType)) && (
          <div ref={labelRef}
            className={`store-name-placeholder ${styles.storeNamePlaceholder}`}
            style={{ display: currentHoveredObject ? 'block' : 'none' }}
          >{currentHoveredObject && currentHoveredObject.userData.storeName}</div>
        )}
      </div>

      {mapConfig.ROLE === 'WP_SITE' &&
        <RetailerInfoModal
          show={showRetailerInfo}
          onHide={() => setShowRetailerInfo(false)}
          amenity={selectedAmenityType}
          selectedAmenityMapObjName={selectedAmenityMapObjName}
          mapObjectName={selectedActiveObjectId}
          onClickMapObj={handleRetailerInfoOnClick}
          allSvgObjectIds={allSvgObjectIds}
        />
      }
    </>
  )
});



export default SceneComponent;
