import { Color } from "three";

export const hex_to_color = (hex_code: string) => {
  const withoutHex = hex_code.replace('#', '');
  return new Color(withoutHex.length === 6 ? parseInt('0x' + withoutHex, 16) : withoutHex);
}

export const getSvgIconHTML = (icon: string) => {
  let svg;
  const svg_block = document.getElementById(`map-special-svg-${icon}`);
  if (svg_block) {
    const svg_element = svg_block.querySelector('svg');
    if (svg_element) {
      const clonedSvgElement = svg_element.cloneNode(true) as SVGElement;
      const svgColoredParts = clonedSvgElement.querySelectorAll('[fill]');
      svgColoredParts.forEach((svgColoredPart) => {
        svgColoredPart.setAttribute('fill', '#FFFFFF');
      });
      svg = (new XMLSerializer()).serializeToString(clonedSvgElement);
    }
  }
  return svg;
}

export const FormatPhoneNumber = (numberStr: string): string => {
  numberStr = numberStr.slice(-10);
  if (numberStr.length === 10) {
    const maskedNumber = `${numberStr.slice(0, 3)}.${numberStr.slice(3, 6)}.${numberStr.slice(-4)}`;
    return maskedNumber;
  }
  return numberStr;
}

