import { Graph, Link, Node } from "ngraph.graph";
import { PathFinder } from "ngraph.path";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { IExtMesh } from "src/interfaces";
import { AppFloor } from "src/interfaces/mapbox";
import { BufferGeometry, CanvasTexture, CurvePath, DoubleSide, LineCurve3, Mesh, MeshBasicMaterial, Object3D, Raycaster, Scene, TubeGeometry, Vector3 } from "three";
import useMaterialAndGeometry from "./useMaterialAndGeometry";
import { useCallback } from "react";

const consolePrefix = 'ROUTE: ';
const FLOOR_CHANGE_PENALTY = 10000;

interface IelevatorNodeInfo {
  node: any;
  escalatorId: string;
  isExitOnly: boolean;
  isEntranceOnly: boolean;
  upOnly: boolean;
  downOnly: boolean;
  direction: string;
  floorsTo: number[];
}

const useRouteHelper = () => {
  const { floorsData, allNodesFloor, ngraphPath, pathFinderGraph, allIndexedMapObjects } = useMapBoxContext();
  const { getRenderOrder } = useMaterialAndGeometry();

  const getDistance = useCallback((fromNode: Node<any>, toNode: Node<any>, link: Link<any>) => {
    if (!link.data) return false;

    let weight = link.data.weight;

    // Add additional penalty if changing floors
    const fromFloor = allNodesFloor[fromNode.id];
    const toFloor = allNodesFloor[toNode.id];

    if (fromFloor !== toFloor) {
      // when changing floor give a penalty to avoid taking escalator when not needed
      // also give an additional small penalty based on the floor difference, to avoid considering switching 6 floors is the same as 2 floors
      return weight + FLOOR_CHANGE_PENALTY + Math.abs(fromFloor - toFloor)
    }

    return weight;
  }, [allNodesFloor])

  const delete_route_path = useCallback((floors: AppFloor[]) => {
    for (let floor_index = 0; floor_index < floors.length; floor_index++) {
      const floor = floors[floor_index];
      floor.route_active = false;

      if (floor.route_tube != null) {
        floor.route_tube.geometry.dispose();
        //@ts-ignore
        floor.route_tube.material.dispose();
        floor.objsGroup?.remove(floor.route_tube);
      }

      floor.route_tube = undefined;
      floor.route_texture = null;
      floor.escalatorMeshes = [];
    }
  }, [])

  const get_route_nodes = useCallback((from_node_name: string, to_node_name: string, aStarPathFinder: PathFinder<any>) => {
    let routeNodes = aStarPathFinder.find(from_node_name, to_node_name);
    if (routeNodes.length === 0) return false;
    return routeNodes;
  }, [])

  const get_route_texture = useCallback((distance: number, floor_index: number, floors: AppFloor[]) => {
    let canvas = document.createElement("canvas");
    let ctx = canvas.getContext("2d");
    if (ctx) {
      ctx.canvas.width = distance;
      ctx.canvas.height = 80;

      ctx.fillStyle = "#4ea5ff";
      ctx.fillRect(0, 0, distance, 80);

      let grd_width = 0.1 * distance;
      const min_grd_width = 60;
      const max_grd_width = 120;
      if (grd_width < min_grd_width) grd_width = min_grd_width;
      if (grd_width > max_grd_width) grd_width = max_grd_width;

      let grd = ctx.createLinearGradient(0, 0, grd_width, 0);
      grd.addColorStop(0, "#4ea5ff");
      grd.addColorStop(0.4, "white");
      grd.addColorStop(0.6, "white");
      grd.addColorStop(1, "#4ea5ff");

      ctx.fillStyle = grd;
      ctx.fillRect(10, 0, grd_width, 80);

      let route_texture = new CanvasTexture(ctx.canvas);
      route_texture.offset.x = 1;
      route_texture.colorSpace = 'srgb';

      floors[floor_index].route_texture = route_texture;

      return route_texture;
    } else {
      return undefined;
    }
  }, [])

  const create_route_path = useCallback((routeNodes: Node<any>[], floor_index: number, floors: AppFloor[], style = '2D') => {
    let distance = 0;
    let route_path = new CurvePath<Vector3>();
    for (let i = 1; i < routeNodes.length; i++) {
      let from_node = new Vector3(routeNodes[i - 1].data.x, routeNodes[i - 1].data.y, 0);
      let to_node = new Vector3(routeNodes[i].data.x, routeNodes[i].data.y, 0);
      route_path.add(new LineCurve3(from_node, to_node));
      distance += from_node.distanceTo(to_node);
    };
    var geometry: BufferGeometry = new TubeGeometry(route_path, 400, 6, 8);
    geometry = new BufferGeometry().copy(geometry);
    var material = new MeshBasicMaterial({
      map: get_route_texture(distance, floor_index, floors),
      transparent: true,
      side: DoubleSide,
      depthTest: false,
      depthWrite: false,
    });

    let route_tube = new Mesh(geometry, material) as IExtMesh;
    if (style === '2D') {
      route_tube.position.z = -10;
      route_tube.scale.z = 0.1;
    }
    route_tube.position.z = 0;
    route_tube.renderOrder = getRenderOrder('route-tube');
    route_tube.mesh_type = 'route-tube';
    if (route_tube.geometry.index !== null) {
      route_tube.geometry.setDrawRange(0, route_tube.geometry.index.count);
    } else {
      console.warn('route_tube.geometry.index is null');
    }
    // draw_route_tube(route_tube, geometry.attributes.position.count, floors);

    floors[floor_index].route_tube = route_tube;
    floors[floor_index].objsGroup?.add(route_tube);

    return route_tube;
  }, [getRenderOrder, get_route_texture])

  const create_route_paths = useCallback((from_mesh_object_id: string, to_mesh_object_id: string, scene: Scene, allNodesFloor: Record<string, number>, escalator_nodes: any[], floors: AppFloor[], pathFinderGraph: Graph, style: string, routeCallback = null) => {
    // TODO: remove scene dependency and scene.getObjectByProperty
    const routePaths = [] as any[];
    delete_route_path(floors);
    let from_obj = scene.getObjectByProperty('object_id', from_mesh_object_id) as IExtMesh;
    let to_obj = scene.getObjectByProperty('object_id', to_mesh_object_id) as IExtMesh;
    let from_node_name = from_obj.route_node_id;
    let to_node_name = to_obj.route_node_id;

    if (!from_node_name) console.error(consolePrefix + 'Route Anchor point missing for "%s"', from_mesh_object_id);
    if (!to_node_name) console.error(consolePrefix + 'Route Anchor point missing for "%s"', to_mesh_object_id);

    if (!from_node_name || !to_node_name) return routePaths;

    const from_route_floor_index = from_obj.floor_index;
    const to_route_floor_index = to_obj.floor_index;

    const aStarPathFinder = ngraphPath.nba(pathFinderGraph, {
      distance: getDistance,
      oriented: true,
      blocked(fromNode, toNode, link) {
        return link.data.blocked;
      },
      //@ts-ignore
      debug: true,
    });
    const routeNodes = get_route_nodes(from_node_name, to_node_name, aStarPathFinder);
    const routeNodesEsclators = [];
    if (!routeNodes) {
      return [];
    }
    // reorder in opposite order because aStarPathFinder.find returns path from the destination to the start
    routeNodes.reverse()
    const floor_routes = [];

    for (let index = 0; index < routeNodes.length; index++) {
      const routeNode = routeNodes[index];
      if (floor_routes[allNodesFloor[routeNode.id]]) {
        floor_routes[allNodesFloor[routeNode.id]].push(routeNode);
      } else {
        floor_routes[allNodesFloor[routeNode.id]] = [routeNode];
      }

      if (escalator_nodes.includes(routeNode.id) && escalator_nodes.includes(routeNodes[index - 1].id) && escalator_nodes.includes(routeNodes[index + 1].id)) {
        // don't consider these middle escalators nodes
        routeNodesEsclators.push(routeNode);
      } else {
        routeNodesEsclators.push(routeNode);
      }
    }
    for (let index = 0; index < routeNodesEsclators.length; index++) {
      const routeNode = routeNodesEsclators[index];
      if (
        allNodesFloor[routeNodesEsclators[0].id] !== allNodesFloor[routeNodesEsclators[routeNodesEsclators.length - 1].id] // proceed only if the start node and end node is not on the same floor
        && escalator_nodes.includes(routeNode.id) // proceed only if the current node is a escalator
      ) {
        const escalator_id = Object.keys(floors[allNodesFloor[routeNode.id]].escalatorsNodes as object).find(key => floors[allNodesFloor[routeNode.id]]?.escalatorsNodes?.[key] === routeNode.id);
        const escalator_mesh: IExtMesh = scene.getObjectByProperty('escalator_id', allNodesFloor[routeNode.id] + '-' + escalator_id) as IExtMesh;

        let goToFloor = null;

        if (allNodesFloor[routeNode.id] !== allNodesFloor[routeNodesEsclators[index + 1].id] && escalator_nodes.includes(routeNodesEsclators[index + 1].id)) {
          goToFloor = {
            index: allNodesFloor[routeNodesEsclators[index + 1].id],
            direction: 'To',
          };
        }

        if (allNodesFloor[routeNode.id] !== allNodesFloor[routeNodesEsclators[index - 1].id] && escalator_nodes.includes(routeNodesEsclators[index - 1].id)) {
          goToFloor = {
            index: allNodesFloor[routeNodesEsclators[index - 1].id],
            direction: 'From',
          };
        }


        if (goToFloor && escalator_mesh) {
          escalator_mesh.goToFloor = goToFloor;
          const escalator_clone = escalator_mesh.clone();
          escalator_clone.goToFloor = goToFloor;
          escalator_clone.object_id = escalator_id
          //@ts-ignore
          floors[allNodesFloor[routeNode.id]]?.escalatorMeshes?.push(escalator_clone);
        }
      }

    }
    for (let floor_index = 0; floor_index < floor_routes.length; floor_index++) {
      const floor_route = floor_routes[floor_index];
      if (floor_route && floor_route.length > 1) {
        const routePath = create_route_path(floor_route, floor_index, floors, style);
        routePaths[floor_index] = routePath;

        floors[floor_index].route_active = true;
        floors[floor_index].routeMeshes = [routePath];
        if (floor_index === from_route_floor_index) {
          //@ts-ignore
          floors[floor_index]?.routeMeshes?.push(from_obj);
        }
        if (floor_index === to_route_floor_index) {
          //@ts-ignore
          floors[floor_index]?.routeMeshes?.push(to_obj);

        }
      }
    }

    return routePaths;
  }, [create_route_path, get_route_nodes, ngraphPath, delete_route_path, getDistance])

  const create_route = useCallback((allNodesFloor: Record<string, number>, from_mesh_object_id: string, to_mesh_object_id: string, scene: Scene, floors: AppFloor[], escalator_nodes: string[], pathFinderGraph: Graph, style: string): {
    routePaths: Mesh[],
    fromFloor?: number
  } => {
    if (from_mesh_object_id === to_mesh_object_id) {
      return {
        routePaths: [],
        fromFloor: undefined
      };
    }
    const routePaths = create_route_paths(from_mesh_object_id, to_mesh_object_id, scene, allNodesFloor, escalator_nodes, floors, pathFinderGraph, style);
    if (!routePaths.length) console.warn(consolePrefix + 'No route found in between "%s" and "%s"', from_mesh_object_id, to_mesh_object_id);

    let from_obj = scene.getObjectByProperty('object_id', from_mesh_object_id) as IExtMesh;
    return {
      routePaths,
      fromFloor: from_obj.floor_index
    };
  }, [create_route_paths])



  //TODO
  const get_nodes_distance = useCallback((from_node_name: string, to_node_name: string, aStarPathFinder: PathFinder<any>, pathFinderGraph: Graph) => {
    let distance = 0;

    let routeNodes = aStarPathFinder.find(to_node_name, from_node_name);
    if (routeNodes.length === 0) return false;

    for (let i = 1; i < routeNodes.length; i++) {
      let link = pathFinderGraph.getLink(routeNodes[i - 1].id, routeNodes[i].id);
      distance += link?.data.weight || 0;
    };
    return distance;
  }, [])

  const linkFloorEscalators = useCallback(() => {
    if (floorsData.length > 1) {
      const escalatorsByFloors: { [k: string]: IelevatorNodeInfo }[] = []
      const escalatorIdsByFloors: string[][] = []
      for (let i = 0; i < floorsData.length; i++) {
        escalatorIdsByFloors[i] = []
        if (!floorsData[i].escalatorsNodes) continue
        escalatorsByFloors[i] = Object.fromEntries(
          Object.entries(floorsData[i].escalatorsNodes).map(([elevatorString, value]) => {
            const splitResult = elevatorString.split('-')
            // Keep only first two parts of the elevatorString (escalator-N-xx-xx)
            const escalatorId = splitResult.slice(0, 2).join('-');

            const isExitOnly = splitResult.includes('end')
            const isEntranceOnly = splitResult.includes('start')
            // if there is at least 3 split and the last is a number
            const upOnly = splitResult.includes('up')
            const downOnly = splitResult.includes('down')
            const direction = upOnly ? 'up' : downOnly ? 'down' : 'both'
            escalatorIdsByFloors[i].push(escalatorId)

            return [escalatorId, {
              node: value,
              escalatorId,
              isExitOnly,
              isEntranceOnly,
              upOnly,
              downOnly,
              direction,
              floorsTo: []
            }];
          })
        );
      }

      for (let i = 0; i < floorsData.length; i++) {
        for (let j = 0; j < escalatorIdsByFloors[i].length; j++) {
          const fromEscalatorId = escalatorIdsByFloors[i][j]
          const fromEscalator = escalatorsByFloors[i][fromEscalatorId]

          for (let floor = i + 1; floor < floorsData.length; floor++) {
            for (let k = 0; k < escalatorIdsByFloors[floor].length; k++) {
              const toEscalatorId = escalatorIdsByFloors[floor][k]
              if (fromEscalatorId === toEscalatorId) {
                fromEscalator.floorsTo.push(floor)
              }
            }
          }
        }
      }
      for (let i = 0; i < (floorsData.length - 1); i++) {
        const floor_escalators = escalatorsByFloors[i];
        for (const escalatorId in floor_escalators) {
          const nodeIdFromDetails = floor_escalators[escalatorId];
          const nodeIdFrom = nodeIdFromDetails.node

          nodeIdFromDetails.floorsTo.forEach((floorTo) => {
            const nodeIdToDetails = escalatorsByFloors[floorTo][escalatorId]
            const nodeIdTo = nodeIdToDetails.node
            if (nodeIdTo) {
              if (nodeIdFromDetails.isExitOnly) {
                // end of escalator, can't enter
                // add the opposite direction only
                pathFinderGraph.addLink(nodeIdFrom, nodeIdTo, { blocked: true });
                pathFinderGraph.addLink(nodeIdTo, nodeIdFrom, { weight: 1 });
              } else if (nodeIdFromDetails.isEntranceOnly) {
                pathFinderGraph.addLink(nodeIdTo, nodeIdFrom, { blocked: true });
                pathFinderGraph.addLink(nodeIdFrom, nodeIdTo, { weight: 1 });
              } else {
                pathFinderGraph.addLink(nodeIdFrom, nodeIdTo, { weight: 1 });
                pathFinderGraph.addLink(nodeIdTo, nodeIdFrom, { weight: 1 });
              }
            }
          })
        }
      }
    }
  }, [floorsData, pathFinderGraph])

  const make_amenity_route = useCallback((kiosk_obj_name: string, amenity_type: string, scene: Scene, escalator_nodes: string[], style: string): {
    routePaths: Mesh[],
    fromFloor?: number
  } => {
    let kiosk_obj: IExtMesh = scene.getObjectByProperty('object_id', kiosk_obj_name) as IExtMesh;
    let kiosk_node_name = kiosk_obj ? kiosk_obj.route_node_id : '';

    var to_obj_name = null;

    var shortest_distance = 9999;
    const map_objs: string[] = [];
    for (let key in allIndexedMapObjects) {
      const mapObject = allIndexedMapObjects[key];
      if (
        mapObject.layer_type === 'amenity'
        && mapObject.obj_type === 'special'
        && mapObject.value === amenity_type
      ) {
        map_objs.push(key);
      }
    }
    map_objs.forEach((amenity_obj_name: string) => {
      let amenity_obj: IExtMesh = scene.getObjectByProperty('object_id', amenity_obj_name) as IExtMesh;

      const aStarPathFinder = ngraphPath.aStar(pathFinderGraph, {
        distance: getDistance,
        //@ts-ignore
        debug: true,
      });

      if (amenity_obj && kiosk_node_name) {
        var amenity_node_name = amenity_obj.route_node_id;
        try {
          var route_distance = get_nodes_distance(kiosk_node_name || '', amenity_node_name || '', aStarPathFinder, pathFinderGraph);
          if (route_distance && route_distance < shortest_distance) {
            shortest_distance = route_distance;
            to_obj_name = amenity_obj_name;
          }
        } catch (e: any) {
          console.warn(`Amenity Path: ${e.message}`);
        }
      }
      if (!kiosk_node_name && amenity_obj) {
        to_obj_name = amenity_node_name;
      }
    });
    return create_route(allNodesFloor, kiosk_obj_name, to_obj_name ?? '', scene, floorsData, escalator_nodes, pathFinderGraph, style);
  }, [allIndexedMapObjects, allNodesFloor, create_route, floorsData, get_nodes_distance, ngraphPath, pathFinderGraph, getDistance])

  const assign_route_nodes_to_stores = useCallback((floor_index: number, escalator_nodes: string[], find_store_node_raycaster: Raycaster) => {
    const result = new Map();
    const currentFloor = floorsData[floor_index];
    if (currentFloor && currentFloor.route_points) {
      currentFloor.route_points.forEach(point => {
        let origin_point = new Vector3(point.node.x, point.node.y, -10);
        let target_direction = new Vector3(0, 0, 1);
        find_store_node_raycaster.set(origin_point, target_direction);
        var storeIntersects = find_store_node_raycaster.intersectObjects(floorsData[floor_index].interactiveObjs);
        if (storeIntersects.length) {
          //@ts-ignore
          storeIntersects[0].object.route_node_id = point.name;
          //@ts-ignore
          result.set(storeIntersects[0].object.object_id, point.name);
          // angrej check and remove this if not needed
          // if (false && mapConfig.ROLE !== 'PORTAL') {
          //   //@ts-ignore
          //   var retailer_id = allIndexedMapObjects[storeIntersects[0].object.object_id].retailer_id;
          //   if (retailer_id != null && allIndexedRetailers[retailer_id]) {
          //     // ngCtrlElem.scope().allRetailers[retailer_index].has_route = true;
          //   }
          // }
        }

        var escalatorIntersects = find_store_node_raycaster.intersectObjects(floorsData[floor_index].escalatorsObjs as Object3D[]);
        if (escalatorIntersects.length) {
          const obj: IExtMesh = escalatorIntersects[0].object as IExtMesh;
          let escalator_obj_id = obj.object_id;
          const escalatorNodes = floorsData[floor_index].escalatorsNodes;
          if (escalatorNodes) {
            escalatorNodes[escalator_obj_id as string] = point.name;
            allNodesFloor[point.name] = floor_index;
            escalator_nodes.push(point.name);
          }
        }
      });
    }
    return result;
  }, [allNodesFloor, floorsData])

  return {
    delete_route_path,
    create_route,
    create_route_paths,
    create_route_path,
    get_route_nodes,
    get_nodes_distance,
    get_route_texture,
    linkFloorEscalators,
    make_amenity_route,
    assign_route_nodes_to_stores
  }
}

export default useRouteHelper;