import React, { MutableRefObject, forwardRef, useImperativeHandle } from 'react';
import SceneComponent from './components/SceneComponent/SceneComponent';
import MapBoxContextProvider, { ProviderInitialData } from './contexts/MapBoxContextProvider';
import { AllAmenities, MapAmenityID } from './helpers/amenities.helper';
import SVGIcons from './helpers/svgIcons.helper';
import { MapAllDataResponse, MapCameraControlsState, MapObjData } from './interfaces/mapApiTypes';
import { MapConfigProps } from './interfaces';
import 'bootstrap/dist/css/bootstrap.min.css';
import './styles/global.scss';
import styles from './MapBox.module.scss';


export type MapBoxRefProps = {
  refreshData: () => void,
  createRouteToAmenity: (amenityId: MapAmenityID) => void;
  createRouteToStore: (retailerId: number) => void;
  getInitialDistance: () => number;
  resetMap: () => void;
  refreshState: () => void;
  setMapConfig: React.Dispatch<React.SetStateAction<MapConfigProps>>;
  refetchMapData: () => void;
}

export interface AppProps {
  config: Partial<MapConfigProps>;
  webApiURI: string;
  onSubmit?: (data: MapObjData, refreshData?: () => void) => void;
  mapBoxRefObj?: MutableRefObject<MapBoxRefProps | null> | undefined,
  onResetData?: (data: MapObjData) => void;
  onChangeData?: (data: MapObjData) => void;
  onNewData?: (data: MapObjData) => void;
  mapApiResponseRef?: MutableRefObject<MapAllDataResponse | null> | undefined;
  onChangeCameraControlPosition?: (cameraControlPos: MapCameraControlsState) => void;
  onExtractedAmenities?: (amenityIds: MapAmenityID[]) => void;
  handleClickOnRetailer?: (retailer_id: number) => void;
}

const MapBox = forwardRef((params: AppProps, ref) => {
  const {
    config,
    webApiURI,
    onSubmit,
    mapBoxRefObj,
    onChangeCameraControlPosition,
    onResetData,
    onChangeData,
    onNewData,
    mapApiResponseRef,
    onExtractedAmenities,
    handleClickOnRetailer
  } = params;
  const [selectedActiveObjectId, setSelectedActiveObjectId] = React.useState<string>('');

  useImperativeHandle(ref, () => {
    return {
      getInitialDistance: mapBoxRefObj?.current?.getInitialDistance
    }
  });

  const handleOnSubmit = (data: MapObjData, refreshData?: () => void) => {
    if (onSubmit) {
      onSubmit(data, refreshData);
    }
  }

  const providerInitialData: ProviderInitialData = {
    webApiBaseUrl: webApiURI,
    config
  }

  return (
    <MapBoxContextProvider mapApiResponseRef={mapApiResponseRef} initialData={providerInitialData}>
      <div className={`mapBoxComponent ${styles.mapBoxComponent} MAP_ROLE_${config.ROLE} ${config.ROLE !== 'PORTAL' ? styles.view : ''}`}>
        <SceneComponent
          ref={mapBoxRefObj}
          setSelectedActiveObjectId={setSelectedActiveObjectId}
          selectedActiveObjectId={selectedActiveObjectId}
          onSubmit={handleOnSubmit}
          onResetData={onResetData}
          onChangeData={onChangeData}
          onNewData={onNewData}
          onChangeCameraControlPosition={onChangeCameraControlPosition}
          onExtractedAmenities={onExtractedAmenities}
          handleClickOnRetailer={handleClickOnRetailer}
        />

        <div className={styles.hide}>
          <div id={`map-special-svg-kiosk`} dangerouslySetInnerHTML={{ __html: SVGIcons.Kiosk }}></div>
          <div id={`map-special-svg-escalator`} dangerouslySetInnerHTML={{ __html: SVGIcons.Escalator }}></div>

          {AllAmenities.map((amenity, index) => (
            <div key={index} id={`map-special-svg-${amenity.type}`} dangerouslySetInnerHTML={{ __html: amenity.svgCode }}></div>
          ))}
        </div>

        {/*
        don't delete below div,
        its used for rendering the svg images temporarily to find the image width and height,
        this was added to fix the issue with firefox,
        firefox wasn't able to calculate width and height of the SVG images,
        it was returning 0,0
        */}
        <div id='temp-image-rendering' className={styles.tempImageRendering}></div>
      </div>
    </MapBoxContextProvider>
  );
})

export default MapBox;
