import { MapObjData } from "src/interfaces/mapApiTypes";
import { useCallback, useEffect, useRef, useState, useTransition } from "react";
import { ArrowClockwise } from "react-bootstrap-icons";
import { DoubleSide, MeshBasicMaterial, MeshPhongMaterial, PlaneGeometry, Vector3 } from "three";
import { TextGeometry } from "three-stdlib";
import { IExtMesh, MeshFloorsProps } from "src/interfaces";
import textFontInterface from 'src/Hooks/useMeshFloors/Montserrat_Regular.typeface.json'
import { FontData, useFont } from "@react-three/drei";
import Select, { ActionMeta, SingleValue } from 'react-select';
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { mapObjDefaultVars } from "src/defaults";
import { MapAmenityOption, MapKioskOption, MapRetailerOption } from "src/interfaces/portal-sidebar-form";
import useDrawLogoHelper from "src/Hooks/useMeshFloors/useDrawLogoHelper";
import useMaterialAndGeometry, { MeshType } from "src/Hooks/useMeshFloors/useMaterialAndGeometry";
import { AllAmenities, MapAmenityID } from "src/helpers/amenities.helper";
import styles from './PortalSidebarForm.module.scss'

interface PortalSidebarFormProps {
  floorIndex: number;
  meshFloors: MeshFloorsProps;
  setData: (index: number, data: MapObjData) => void;
  selectedId: string;
  onSubmit?: (data: MapObjData, refreshData?: () => void) => void;
  onResetData?: (data: MapObjData) => void;
  onChangeData?: (data: MapObjData) => void;
  onNewData?: (data: MapObjData) => void;
  triggerSceneUpdate: Function;
}

const incDecKeys = ['size', 'rotate', 'offset_x', 'offset_y'] as const;
type IncDecKey = (typeof incDecKeys)[number]

type TIncDecInterval = {
  [Key in IncDecKey]?: number | null;
}

type UpdateMapCenterMarkerFor = (obj: IExtMesh, formData: MapObjData, floorIndex: Number) => void;


const PortalSidebarForm = (params: PortalSidebarFormProps) => {

  const { setData, selectedId, floorIndex, onResetData, onChangeData, onNewData, triggerSceneUpdate } = params;
  const { mapApiResponse, meshObject, allIndexedRetailers } = useMapBoxContext();
  const { getImage, processImage, layer_text_logo_position_by_id } = useDrawLogoHelper();
  const { getMaterial } = useMaterialAndGeometry();

  const textFont = useFont(textFontInterface as unknown as FontData);
  const [objType, setObjType] = useState<MapObjData['obj_type']>('retailer');
  const [layerType, setLayerType] = useState<MapObjData['layer_type']>('retail_name');

  const [formData, setFormData] = useState<MapObjData>(mapObjDefaultVars);
  const [oldMapObjName, setOldMapObjName] = useState<string | null>(null);
  const [, startTransition] = useTransition();
  const intervalRefs = useRef<TIncDecInterval>({});

  const [selectedKiosksId, setSelectedKiosksId] = useState<number[]>([]);

  const recheckSelectedKiosksId = useCallback((mapObjs: any[]) => {
    if (mapObjs) {
      const ids = (mapObjs as { kiosk_id: number }[]).reduce<number[]>((prev, cur) => {
        if (cur.kiosk_id !== null) {
          return [...prev, cur.kiosk_id];
        }
        return prev;
      }, [])
      setSelectedKiosksId([...ids]);
    }
  }, []);

  useEffect(() => {
    recheckSelectedKiosksId(mapApiResponse.map_objs);
  }, [mapApiResponse, recheckSelectedKiosksId])

  const updateMapCenterMarkerFor: UpdateMapCenterMarkerFor = (obj, formData, floorIndex) => {
    const kioskConfig = obj.userData.kioskConfig;
    if (kioskConfig) {
      obj.userData.distanceFactor = formData?.lock_size ? 1500 : undefined;
      obj.userData.kioskConfig = {
        ...kioskConfig,
        lockSize: formData.lock_size,
        blink: kioskConfig.blink,
        size: Number(formData.size),
        floorIndex: floorIndex,
        text_color: formData.text_color,
      }
      // obj.userData.htmlContent = <MapCenterMarker lockSize={formData.value === '1'} blink={true} size={Number(formData.size)} />
    } else {
      obj.userData.kioskConfig = {
        lockSize: formData.lock_size,
        blink: false,
        size: Number(formData.size),
        floorIndex: floorIndex,
        text_color: formData.text_color,
      }
    }
  }

  const makeImage = useCallback(async (formData: MapObjData, obj: IExtMesh) => {
    if (oldMapObjName !== formData?.map_obj_name) return;
    let imgUrl = '';
    if (formData.layer_type === 'retail_logo') {
      imgUrl = await getImage(formData, allIndexedRetailers[formData.retailer_id || 0]);
    } else {
      imgUrl = await getImage(formData);
    }
    if (imgUrl) {
      processImage(imgUrl, formData, (geometry: PlaneGeometry, material: MeshBasicMaterial) => {
        obj.geometry = geometry;
        obj.material = material;

        const boundingBox = geometry.boundingBox;

        const mesh_center_point = new Vector3();
        if (boundingBox) {
          boundingBox.getCenter(mesh_center_point);

          const mesh_size = new Vector3();
          boundingBox.getSize(mesh_size);
          if (obj.object_id) {
            layer_text_logo_position_by_id(obj.object_id, mesh_center_point, mesh_size, obj, { [obj.object_id]: { ...formData } });
          }
        }
      });
    }
  }, [getImage, layer_text_logo_position_by_id, oldMapObjName, processImage, allIndexedRetailers])

  const updateTooltip = useCallback((text: string, map_obj_name: string) => {
    params.meshFloors.meshParams.flat().filter(item => item !== undefined).forEach(item => {
      if (item.mesh.object_id === map_obj_name) {
        item.mesh.userData.storeName = text
      }
    })
  }, [params.meshFloors.meshParams])

  const makeTextGeometry = useCallback((obj: IExtMesh, text: string, size: number) => {
    if (!obj) return;
    if (oldMapObjName !== formData?.map_obj_name) return;
    let text_geometry = new TextGeometry(text, {
      font: (obj.userData && obj.userData.font) ? obj.userData.font : textFont,
      size,
      height: 0.01,
      curveSegments: 4,
    });
    text_geometry.center();
    if (obj.geometry) {
      obj.geometry.dispose();
    }
    obj.geometry = text_geometry;
    updateTooltip(text, formData.map_obj_name);
  }, [formData?.map_obj_name, textFont, oldMapObjName, updateTooltip])

  const updateData = useCallback((formData: MapObjData, isUserAction: boolean = false) => {
    const index = mapApiResponse.map_objs.findIndex((value: MapObjData) => value.map_obj_name === formData.map_obj_name);
    if (index !== -1) {
      formData.id = mapApiResponse.map_objs[index].id;
      mapApiResponse.map_objs[index] = { ...formData };
      setData(index, { ...formData });
      recheckSelectedKiosksId(mapApiResponse.map_objs);
      if (isUserAction) {
        onChangeData?.(formData);
      }
    } else {
      if (isUserAction) {
        onNewData?.(formData);
      }
    }
  }, [mapApiResponse, setData, recheckSelectedKiosksId, onNewData, onChangeData])

  const clearKioskConfig = () => {
    if (meshObject) {
      const obj = meshObject[1];
      if (obj && obj.userData && obj.userData.kioskConfig) {
        obj.userData.kioskConfig = null;
        params.meshFloors.storeLogos.flat().filter(item => item !== undefined).forEach(item => {
          if (item.storeLogo.object_id === obj.object_id && item.storeLogo.userData.kioskConfig !== undefined) {
            item.storeLogo.userData.kioskConfig = null;
          }
        });
      }
    }
  }

  const onObjTypeChange = (key: MapObjData['obj_type']) => {
    setObjType(key);
    formData.obj_type = key;
    clearKioskConfig();

    if (key === 'retailer') {
      if (!['retail_name', 'retail_logo', 'retail_text'].includes(layerType)) {
        onLayerTypeChange('retail_name');
      }
      formData.kiosk_id = null;
    }

    if (key === 'special') {
      if (!['kiosk', 'amenity'].includes(layerType)) {
        onLayerTypeChange('kiosk');
      }
      formData.retailer_id = null;
      if (meshObject) {
        const obj = meshObject[1];
        makeTextGeometry(obj, '', formData.size);
        // if (obj) {
        //   if (!obj.userData) {
        //     obj.userData = {};
        //   }
        //   obj.userData.distanceFactor = formData.lock_size ? 1500 : undefined;
        //   obj.userData.kioskConfig = {
        //     lockSize: formData.lock_size,
        //     blink: false,
        //     size: Number(formData.size)
        //   }
        // }
      }
    }

    if (key === 'custom') {
      if (!['custom_text', 'custom_image'].includes(layerType)) {
        onLayerTypeChange('custom_text');
      }
      formData.retailer_id = null;
      formData.kiosk_id = null;
    }

    onValueChange('size', formData.size.toString());
    onValueChange('text_color', formData.text_color || mapApiResponse.mapSettings.STORE_TEXT_COLOR);

    setFormData({ ...formData });
    updateData({ ...formData });
  }

  const onLayerTypeChange = (key: MapObjData['layer_type']) => {
    if (!formData) return;

    setLayerType(key);
    formData.layer_type = key;
    clearKioskConfig();

    if (key === 'retail_name') {
    } else if (key === 'retail_logo') {
    } else if (key === 'retail_text') {
    } else if (key === 'kiosk') {
      formData.value = '';
      if (meshObject) {
        makeImage(formData, meshObject[1]);
      }
    } else if (key === 'amenity') {
      formData.kiosk_id = null;
    } else if (key === 'custom_text') {
    } else if (key === 'custom_image') {
    }

    onValueChange('size', formData.size.toString());

    setFormData({ ...formData });
    updateData({ ...formData });
  }

  const handleDelete = () => {
    if (!formData) return;
    Object.assign(formData, mapObjDefaultVars, { id: formData.id, map_obj_name: formData.map_obj_name });
    // setOldMapObjName('');
    onObjTypeChange('retailer');
    onLayerTypeChange('retail_name');
    updateTooltip('', formData.map_obj_name);

    if (onResetData) {
      onResetData({ ...formData })
    }
  };

  const onRetailerChange = (selectedOption: SingleValue<MapRetailerOption>, actionMeta: ActionMeta<MapRetailerOption>) => {
    if (selectedOption && formData) {
      formData.retailer_id = selectedOption.value;
      onLayerTypeChange(formData.layer_type);
      setFormData({ ...formData });
      updateData({ ...formData }, true);
    }
  };

  const onKioskChange = (selectedOption: SingleValue<MapKioskOption>, actionMeta: ActionMeta<MapKioskOption>) => {
    if (selectedOption && formData) {
      formData.kiosk_id = selectedOption.value;
      setFormData({ ...formData });
      updateData({ ...formData }, true);
      meshObject.forEach((obj, index) => {
        if (index === 0) return;
        updateMapCenterMarkerFor(obj, formData, floorIndex);
        triggerSceneUpdate();
      })
    }
  };

  const onAmenityChange = (selectedOption: SingleValue<MapAmenityOption>, actionMeta: ActionMeta<MapAmenityOption>) => {
    if (selectedOption && formData) {
      formData.value = selectedOption.value as MapAmenityID;
      setFormData({ ...formData });
      updateData({ ...formData }, true);
      meshObject.forEach((obj, index) => {
        if (index === 0) return;
        makeImage(formData, obj);
      })
    }
  };

  const onValueChange = useCallback((name: string, value: string) => {
    if (!formData) return;

    meshObject.forEach((obj, index) => {
      if (!obj.userData) {
        obj.userData = {};
      }
      if (!obj.userData.position) {
        obj.userData.position = new Vector3();
        if (obj.position) {
          obj.userData.position.copy(obj.position);
        }
      }
      const position = new Vector3();
      if (obj.userData.position) {
        position.copy(obj.userData.position);
      }

      switch (name) {
        case 'custom_text':
          if (index === 0) break;
          formData.custom_text = value;
          if (['retail_text', 'custom_text'].includes(formData.layer_type)) {
            makeTextGeometry(obj, formData.custom_text, formData.size);
          }
          break;

        case 'custom_image':
          if (index === 0) break;
          formData.custom_image = value;
          makeImage(formData, obj);
          updateData({ ...formData }, true)
          break;

        case 'size':
          if (index === 0) break;
          formData.size = parseInt(value);
          if (!(['retail_logo', 'kiosk', 'amenity', 'custom_image'].includes(formData.layer_type))) {
            let text: MapObjData['custom_text'] = '';
            if (formData.layer_type === 'retail_name' && allIndexedRetailers[formData.retailer_id || 0]?.retail_name) {
              text = allIndexedRetailers[formData.retailer_id || 0]?.retail_name;
            } else if (['retail_text', 'custom_text'].includes(formData.layer_type)) {
              text = formData.custom_text;
            }
            makeTextGeometry(obj, text || '', formData.size);
          } else if (formData.layer_type === 'retail_logo' || formData.layer_type === 'custom_image') {
            makeImage(formData, obj);
            setTimeout(() => {
              updateData({ ...formData }, true);
            }, 300);
          } else if (formData.layer_type === 'amenity') {
            makeImage(formData, obj);
            startTransition(() => {
              setFormData({ ...formData });
              updateData({ ...formData }, true);
            })
          } else if (formData.kiosk_id && formData.layer_type === 'kiosk') {
            updateMapCenterMarkerFor(obj, formData, floorIndex);
            triggerSceneUpdate();
          }
          break;

        case 'lock_size':
          formData.lock_size = false;
          formData.value = value as MapAmenityID;
          if (formData.layer_type === 'kiosk') {
            updateMapCenterMarkerFor(obj, formData, floorIndex);
            triggerSceneUpdate();
          }
          break;

        case 'rotate':
          if (index === 0) break;
          formData.rotate = parseInt(value);
          obj.rotation.set(
            obj.rotation.x,
            obj.rotation.y,
            formData.rotate * Math.PI / 180
          );
          break;

        case 'offset_x':
          if (index === 0) break;
          position.x += parseFloat(value);
          obj.position.setX(position.x);
          formData.offset_x = parseInt(value);
          break;

        case 'offset_y':
          if (index === 0) break;
          position.y += parseFloat(value);
          obj.position.setY(position.y);
          formData.offset_y = parseInt(value);
          break;

        case 'text_color':
          if (index === 0) break;
          formData.text_color = value;
          if (['retail_name', 'retail_text', 'custom_text'].includes(formData.layer_type)) {
            const material_settings = {
              color: formData.text_color,
              transparent: true,
              side: DoubleSide,
              depthWrite: false,
              depthTest: false,
            };
            let text_material = new MeshPhongMaterial(material_settings);
            obj.material = text_material;
          } else if (
            formData.layer_type === 'retail_logo' ||
            formData.layer_type === 'custom_image' ||
            (formData.obj_type === 'special' && formData.layer_type === 'kiosk' && formData.kiosk_id != null) ||
            (formData.obj_type === 'special' && formData.layer_type === 'amenity' && formData.value !== null)) {
            makeImage(formData, obj);
            if (formData.layer_type === 'kiosk') {
              updateMapCenterMarkerFor(obj, formData, floorIndex);
            }
            startTransition(() => {
              updateData({
                ...formData,
                text_color: formData.text_color && formData.text_color.replace('#', ''),
              }, true);
            })
          }
          break;

        case 'bg_color':
          if (index !== 0) break;
          formData.bg_color = value;
          let meshType: MeshType = 'store';
          if (obj.object_id?.startsWith('big-store')) {
            meshType = 'big-store';
          }
          obj.material = getMaterial(
            meshType,
            formData.map_obj_name,
            formData.bg_color,
            formData.transparent
          );
          break;

        default:
          break;
      }
      startTransition(() => {
        setFormData({ ...formData })
        updateData({ ...formData }, true);
      });

    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [floorIndex, formData, getMaterial, makeImage, makeTextGeometry, mapApiResponse.retailers, meshObject, triggerSceneUpdate, updateData])

  const onFieldChange = useCallback((e: any) => {
    const target = e.target as HTMLInputElement;
    onValueChange(target.name, target.value);
  }, [onValueChange])

  useEffect(() => {
    if (selectedId) {
      // todo detect when more than 1, do something
      // const searchRes = mapApiResponse.map_objs.filter((value: any) => value.map_obj_name === selectedId);
      const index = mapApiResponse.map_objs.findIndex((value: any) => value.map_obj_name === selectedId);
      if (index !== -1) {
        const mapObjData = { ...mapApiResponse.map_objs[index] };
        setFormData(mapObjData);
        setObjType(mapObjData.obj_type);
        setLayerType(mapObjData.layer_type);
      } else {
        setFormData({
          ...mapObjDefaultVars,
          map_obj_name: selectedId,
          id: (new Date()).getTime()
        });
      }
    }
  }, [mapApiResponse, selectedId]);

  useEffect(() => {
    if (!formData) return;
    setOldMapObjName(formData.map_obj_name);

    // if (formData.obj_type) {
    //   setOldMapObjName(formData.map_obj_name);
    //   onObjTypeChange(formData.obj_type);
    // }
    // if (formData.layer_type) {
    //   onLayerTypeChange(formData.layer_type);
    //   setOldMapObjName(formData.map_obj_name);
    // }
    // don't rerun when onTabChange is changed1
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formData])

  const handleDecrement = (key: IncDecKey) => {
    if (!formData) return;
    if (Object.hasOwn(formData, key)) {
      onValueChange(key, (formData[key] - 1).toString());
    }
  };

  const handleIncrement = (key: IncDecKey) => {
    if (!formData) return;
    if (Object.hasOwn(formData, key)) {
      onValueChange(key, (formData[key] + 1).toString());
    }
  };

  const handleRotationInSteps = (key: IncDecKey, angle: number = 45) => {
    if (!formData) return;

    if (Object.hasOwn(formData, key)) {
      let value = formData[key];
      // Adjust the current value to the nearest lower multiple of the angle
      const remainder = value % angle;
      if (remainder !== 0) {
        value -= remainder;
      }

      // Increment by the angle
      value += angle;

      if (value > 360) {
        value = angle;
      } else if (value === 360) {
        value = 0;
      }

      onValueChange(key, value.toString());
    }
  };

  const startDecrementing = (key: IncDecKey) => {
    const id = window.setInterval(() => {
      handleDecrement(key);
    }, 200);
    intervalRefs.current[key] = id;
  };

  const startIncrementing = (key: IncDecKey) => {
    const id = window.setInterval(() => {
      handleIncrement(key);
    }, 200);
    intervalRefs.current[key] = id;
  }

  const stopInterval = (key: IncDecKey) => {
    if (intervalRefs.current[key]) {
      clearInterval(intervalRefs.current[key] as number);
      intervalRefs.current[key] = null;
    }
  };

  const retailerOptions: MapRetailerOption[] = mapApiResponse?.retailers.map((retailer) => {
    return {
      value: retailer.id,
      label: mapApiResponse?.globalRetailerIds.filter(id => id === retailer.global_retailer_id).length > 1 ? `${retailer.retail_name} (${retailer.location})` : retailer.retail_name
    }
  }) || [];

  const kioskOptions: MapKioskOption[] = mapApiResponse?.kiosks.map((kiosk) => {
    return {
      value: kiosk.id,
      label: kiosk.name,
      isDisabled: selectedKiosksId.includes(kiosk.id),
    }
  }) || [];

  const amenityOptions: MapAmenityOption[] = AllAmenities.map((amenity) => {
    return {
      value: amenity.type as string,
      label: amenity.name,
    }
  }) || [];

  if (!formData) return null;

  return (
    <div className={`sidebarComp ${styles.sidebarComp}`}>

      <div className={styles.sidebarFields}>
        <div className={styles.tabOptions}>
          <div className={styles.mainBtnsGroup}>
            <div className={styles.btnsGroup}>
              <button onClick={() => onObjTypeChange('retailer')} className={objType === 'retailer' ? styles.active : ''}>Retailer</button>
              <button onClick={() => onObjTypeChange('special')} className={objType === 'special' ? styles.active : ''}>Special</button>
              <button onClick={() => onObjTypeChange('custom')} className={objType === 'custom' ? styles.active : ''}>Custom</button>
            </div>
            <button className={styles.deleteBtn} onClick={handleDelete}>Delete</button>
          </div>

          {objType === 'retailer' && (
            <>
              <Select
                className={styles.selectField}
                classNamePrefix="custom"
                menuPortalTarget={document.body}
                aria-label="Choose a Retailer"
                options={retailerOptions}
                isLoading={false}
                noOptionsMessage={() => 'Retailers not found'}
                placeholder="Choose a Retailer..."
                value={retailerOptions.find((option) => option.value === formData.retailer_id) || null}
                onChange={onRetailerChange}
              />

              {formData.retailer_id as number > 0 && (
                <div className={styles.btnsGroup}>
                  <button onClick={() => onLayerTypeChange('retail_name')} className={layerType === 'retail_name' ? styles.active : ''}>Retail Name</button>
                  <button onClick={() => onLayerTypeChange('retail_logo')} className={layerType === 'retail_logo' ? styles.active : ''}>Retail Logo</button>
                  <button onClick={() => onLayerTypeChange('retail_text')} className={layerType === 'retail_text' ? styles.active : ''}>Custom Text</button>
                </div>
              )}
            </>
          )}

          {objType === 'special' && (
            <>
              <div className={styles.btnsGroup}>
                <button onClick={() => onLayerTypeChange('kiosk')} className={layerType === 'kiosk' ? styles.active : ''}>Kiosk</button>
                <button onClick={() => onLayerTypeChange('amenity')} className={layerType === 'amenity' ? styles.active : ''}>Amenity</button>
              </div>

              {layerType === 'kiosk' && (
                <Select
                  className={styles.selectField}
                  classNamePrefix="custom"
                  aria-label="Choose a Kiosk"
                  options={kioskOptions}
                  noOptionsMessage={() => 'Kiosks not found'}
                  placeholder="Choose a Kiosk..."
                  value={kioskOptions.find((item) => item.value === formData.kiosk_id) || null}
                  onChange={onKioskChange}
                />
              )}

              {layerType === 'amenity' && (
                <Select
                  className={styles.selectField}
                  classNamePrefix="custom"
                  aria-label="Choose Amenity"
                  options={amenityOptions}
                  noOptionsMessage={() => 'Amenity not found'}
                  placeholder="Choose Amenity..."
                  value={amenityOptions.find((item) => item.value === formData.value) || null}
                  onChange={onAmenityChange}
                />
              )}
            </>
          )}

          {objType === 'custom' && (
            <>
              <div className={styles.btnsGroup}>
                <button onClick={() => onLayerTypeChange('custom_text')} className={layerType === 'custom_text' ? styles.active : ''}>Text</button>
                <button onClick={() => onLayerTypeChange('custom_image')} className={layerType === 'custom_image' ? styles.active : ''}>Image</button>
              </div>

              {layerType === 'custom_image' && (
                <div className={styles.sidebarField}>
                  <label className={styles.label}>Image URL</label>
                  <div className={styles.fieldWrapper}>
                    <input
                      name="custom_image"
                      type="text"
                      className={styles.textField}
                      value={formData.custom_image as string}
                      onChange={onFieldChange}
                    />
                  </div>
                </div>
              )}
            </>
          )}

          {((objType === 'retailer' && layerType === 'retail_text') || (objType === 'custom' && layerType === 'custom_text')) && (
            <div className={styles.sidebarField}>
              <label className={styles.label}>Text</label>
              <div className={styles.fieldWrapper}>
                <textarea
                  name="custom_text"
                  rows={3}
                  className={styles.textareaField}
                  value={formData.custom_text as string}
                  onChange={onFieldChange}
                />
              </div>
            </div>
          )}
        </div>

        <div className={styles.sidebarField}>
          <label className={styles.label}>Size</label>
          <div className={styles.fieldWrapper}>
            <div className={styles.numberFieldGroup}>
              <button
                className={`${styles.button} ${styles.decrement}`}
                onClick={() => handleDecrement('size')}
                onMouseDown={() => startDecrementing('size')}
                onMouseUp={() => stopInterval('size')}
                onMouseLeave={() => stopInterval('size')}
              >-</button>

              <input
                type="number"
                value={formData.size}
                onChange={(e) => onValueChange('size', e.target.value)}
                className={styles.input}
              />

              <button
                className={`${styles.button} ${styles.increment}`}
                onClick={() => handleIncrement('size')}
                onMouseDown={() => startIncrementing('size')}
                onMouseUp={() => stopInterval('size')}
                onMouseLeave={() => stopInterval('size')}
              >+</button>
            </div>

            {/* <label className="checkbox-field">
              <input
                id="lock_size"
                name="lock_size"
                type="checkbox"
                value={formData.value}
                checked={formData.value ? true : false}
                onChange={(e) => onValueChange('lock_size', e.target.value)}
              ></input>
              <span className="tickbox"></span>
              <span className="tick-label">Lock</span>
            </label> */}
          </div>
        </div>

        <div className={styles.sidebarField}>
          <label className={styles.label}>Rotate</label>
          <div className={styles.fieldWrapper}>
            <div className={styles.numberFieldGroup}>
              <button
                className={`${styles.button} ${styles.decrement}`}
                onClick={() => handleDecrement('rotate')}
                onMouseDown={() => startDecrementing('rotate')}
                onMouseUp={() => stopInterval('rotate')}
                onMouseLeave={() => stopInterval('rotate')}
              >-</button>

              <input
                type="number"
                value={formData.rotate}
                onChange={(e) => onValueChange('rotate', e.target.value)}
                className={styles.input}
              />

              <button
                className={`${styles.button} ${styles.increment}`}
                onClick={() => handleIncrement('rotate')}
                onMouseDown={() => startIncrementing('rotate')}
                onMouseUp={() => stopInterval('rotate')}
                onMouseLeave={() => stopInterval('rotate')}
              >+</button>
            </div>
            <button
              className={styles.rotateButton}
              onClick={() => handleRotationInSteps('rotate')}
            >
              <ArrowClockwise size={24} />
            </button>
          </div>
        </div>

        <div className={styles.sidebarField}>
          <label className={styles.label}>Offset X</label>
          <div className={styles.fieldWrapper}>
            <div className={styles.numberFieldGroup}>
              <button
                className={`${styles.button} ${styles.decrement}`}
                onClick={() => handleDecrement('offset_x')}
                onMouseDown={() => startDecrementing('offset_x')}
                onMouseUp={() => stopInterval('offset_x')}
                onMouseLeave={() => stopInterval('offset_x')}
              >-</button>

              <input
                type="number"
                value={formData.offset_x}
                onChange={(e) => onValueChange('offset_x', e.target.value)}
                className={styles.input}
              />

              <button
                className={`${styles.button} ${styles.increment}`}
                onClick={() => handleIncrement('offset_x')}
                onMouseDown={() => startIncrementing('offset_x')}
                onMouseUp={() => stopInterval('offset_x')}
                onMouseLeave={() => stopInterval('offset_x')}
              >+</button>
            </div>
          </div>
        </div>

        <div className={styles.sidebarField}>
          <label className={styles.label}>Offset Y</label>
          <div className={styles.fieldWrapper}>
            <div className={styles.numberFieldGroup}>
              <button
                className={`${styles.button} ${styles.decrement}`}
                onClick={() => handleDecrement('offset_y')}
                onMouseDown={() => startDecrementing('offset_y')}
                onMouseUp={() => stopInterval('offset_y')}
                onMouseLeave={() => stopInterval('offset_y')}
              >-</button>

              <input
                type="number"
                value={formData.offset_y}
                onChange={(e) => onValueChange('offset_y', e.target.value)}
                className={styles.input}
              />

              <button
                className={`${styles.button} ${styles.increment}`}
                onClick={() => handleIncrement('offset_y')}
                onMouseDown={() => startIncrementing('offset_y')}
                onMouseUp={() => stopInterval('offset_y')}
                onMouseLeave={() => stopInterval('offset_y')}
              >+</button>
            </div>
          </div>
        </div>

        <div className={styles.sidebarField}>
          <label className={styles.label}>BG Color</label>
          <div className={styles.fieldWrapper}>
            <div className={styles.colorFieldGroup}>
              <input
                type="color"
                name="bg_color"
                className={styles.colorField}
                value={formData.bg_color as string}
                onChange={onFieldChange}
              />
              <input
                type="text"
                name="bg_color"
                className={styles.inputField}
                value={formData.bg_color as string}
                onChange={onFieldChange}
              />
            </div>
            {/* <label className="checkbox-field">
              <input
                id="transparent"
                name="transparent"
                type="checkbox"
                value={formData.transparent ? 1 : 0}
                checked={formData.value ? true : false}
                onChange={(e) => onValueChange('lock_size', e.target.value)}
              ></input>
              <span className="tickbox"></span>
              <span className="tick-label">Transparent</span>
            </label> */}
          </div>
        </div>

        {['retail_name', 'custom_text', 'retail_text', 'kiosk', 'amenity'].includes(formData.layer_type) && (
          <div className={styles.sidebarField}>
            <label className={styles.label}>{['kiosk', 'amenity'].includes(formData.layer_type) ? 'Icon' : 'Text'} Color</label>
            <div className={styles.fieldWrapper}>
              <div className={styles.colorFieldGroup}>
                <input
                  name="text_color"
                  type="color"
                  className={styles.colorField}
                  value={formData.text_color as string}
                  onChange={onFieldChange}
                />
                <input
                  type="text"
                  name="text_color"
                  className={styles.inputField}
                  value={formData.text_color as string}
                  onChange={onFieldChange}
                />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default PortalSidebarForm;


