import { useThree } from "@react-three/fiber";
import { PerspectiveCamera, Vector3 } from "three";

interface CameraPropertiesProps {
  far?: number;
  near?: number;
  fov?: number;
  aspectRatio?: number;
  position?: Vector3;
}

export const CameraProperties = (props: CameraPropertiesProps) => {
  const { camera } = useThree();

  const cameraProjectionNeedsUpdate = props.far || props.near || props.fov;
  if (props.far) {
    camera.far = props.far;
  }
  if (props.near) {
    camera.near = props.near;
  }

  if (camera instanceof PerspectiveCamera) {
    if (props.fov) {
      camera.fov = props.fov;
    }
  }

  if (cameraProjectionNeedsUpdate) {
    camera.updateProjectionMatrix();
  }

  return null;
}