import { useEffect, useMemo, useState } from "react";
import { SpriteMaterial, Texture } from "three";
import styles from './MapCenterMarker.module.scss';
import { Html } from "@react-three/drei";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";

interface MapCenterMarketProps {
  size: number;
  lockSize?: boolean;
  blink: boolean;
  cameraLength: number | undefined;
  text_color?: string;
}

export const MapCenterMarker = ({ size, lockSize, blink, cameraLength, text_color = "black" }: MapCenterMarketProps) => {
  const { mapConfig } = useMapBoxContext();
  const [, setSizeFactor] = useState<number>(1) // 1 - keep original size value

  const [texture, setTexture] = useState<Texture | null>(null);
  const scale = lockSize ? 5 * size / 256 : 8 * size;

  useEffect(() => {
    if (!lockSize && cameraLength !== undefined) {
      setSizeFactor(1 - (cameraLength - mapConfig.CAMERA.minDistance) / (mapConfig.CAMERA.maxDistance - mapConfig.CAMERA.minDistance))
    }
  }, [lockSize, mapConfig, cameraLength])

  const material = useMemo(() => {
    if (!texture) {
      return null;
    }

    const material = new SpriteMaterial({ map: texture, sizeAttenuation: !lockSize, depthTest: false, depthWrite: false, color: text_color });
    return material;
  }, [lockSize, texture, text_color])

  // todo figure out better way
  useEffect(() => {
    const svgOrigin = document.getElementById('map-special-svg-kiosk')?.querySelector('svg');
    if (svgOrigin) {
      const clonedSvgElement = svgOrigin.cloneNode(true) as SVGElement;
      const svgData = (new XMLSerializer()).serializeToString(clonedSvgElement);
      const svgBlob = new Blob([svgData], { type: 'image/svg+xml' });
      const svgUrl = URL.createObjectURL(svgBlob);
      const canvas = document.createElement('canvas');
      canvas.width = 256//svgSize.width*0.1;
      canvas.height = 256//svgSize.height*0.1;
      const ctx = canvas.getContext('2d');
      const img = document.createElement('img');
      img.src = svgUrl;
      img.onload = () => {
        ctx?.drawImage(img, 96, 0, 64, 128);
        const svgTexture = new Texture(canvas);
        svgTexture.needsUpdate = true;
        setTexture(svgTexture);
      }
    }
  }, []);

  if (material) {
    return (
      <group>
        <sprite scale={scale} material={material}></sprite>
        {blink && <Html><div className={`mapCenterMarker ${styles.mapCenterMarker}`}></div></Html>}
      </group>
    )
  } else {
    return null
  }
}