import { Dispatch, ReactNode, SetStateAction, createContext, useCallback, useContext, useEffect, useMemo, useState } from "react"
import createGraph, { Graph } from "ngraph.graph";
import { Group, type Mesh } from "three";
import path from "ngraph.path";
import { EventedType } from "ngraph.events";
import { MapConfigProps, IExtMesh } from "src/interfaces";
import { MapObjData, MapRetailer, MapAllDataResponse, MapKiosk } from "src/interfaces/mapApiTypes";
import { defaultPortalMapSettings, mapConfigDefaultVars, mapObjDefaultVars } from "src/defaults";
import { AllAmenities } from "src/helpers/amenities.helper";
import { AppDataProps } from "src/interfaces/mapbox";
import { AppFloor } from "src/interfaces/mapbox";
import createApiServices, { ApiServicesProps } from "src/services/index.service";


interface MapBoxContextType {
  apiBaseUrl: string;
  refetchMapData: () => void;
  apiServices: ApiServicesProps;

  loading: boolean;
  setLoading: Dispatch<SetStateAction<boolean>>;

  initialFloorsDataIsLoaded: boolean;
  setInitialFloorsDataIsLoaded: Dispatch<SetStateAction<boolean>>;

  mapConfig: MapConfigProps;
  setMapConfig: React.Dispatch<React.SetStateAction<MapConfigProps>>;

  mapApiResponse: MapAllDataResponse;
  setMapApiResponse: React.Dispatch<React.SetStateAction<MapAllDataResponse>>;

  appData: AppDataProps;
  setAppData: React.Dispatch<React.SetStateAction<AppDataProps>>;

  meshByObjectId: Map<string, Mesh>;
  setMeshByObjectId: Dispatch<SetStateAction<Map<string, Mesh>>>;

  allMapObjects: Array<string>;
  setAllMapObjects: Dispatch<SetStateAction<Array<string>>>;

  allIndexedMapObjects: Record<string, MapObjData>;
  setAllIndexedMapObjects: Dispatch<SetStateAction<Record<string, MapObjData>>>;

  allIndexedRetailers: Record<number, MapRetailer>;
  setAllIndexedRetailers: Dispatch<SetStateAction<Record<number, MapRetailer>>>;

  indexedKiosks: Record<number, MapKiosk>;
  setIndexedKiosks: Dispatch<SetStateAction<Record<number, MapKiosk>>>;

  floorsData: AppFloor[];
  setFloorsData: Dispatch<SetStateAction<AppFloor[]>>;

  allNodesFloor: Record<string, number>;
  setAllNodesFloor: Dispatch<SetStateAction<Record<string, number>>>;

  pathFinderGraph: Graph<any, any> & EventedType;
  setPathFinderGraph: Dispatch<SetStateAction<Graph<any, any> & EventedType>>;
  ngraphPath: typeof path;
  setNgraphPath: Dispatch<SetStateAction<typeof path>>;

  meshObject: IExtMesh[];
  setMeshObject: React.Dispatch<React.SetStateAction<IExtMesh[]>>;

  cameraLength: number | undefined;
  setCameraLength: React.Dispatch<React.SetStateAction<number | undefined>>;
}

const MapBoxContext = createContext<MapBoxContextType | undefined>(undefined);

export interface ProviderInitialData {
  webApiBaseUrl: string;
  config: Partial<MapConfigProps>;
}

interface MapBoxContextProviderProps {
  children: ReactNode;
  initialData: ProviderInitialData;
  mapApiResponseRef?: React.MutableRefObject<MapAllDataResponse | null>;
}

const MapBoxContextProvider = ({ children, initialData, mapApiResponseRef }: MapBoxContextProviderProps) => {
  const apiBaseUrl = initialData.webApiBaseUrl;

  const [loading, setLoading] = useState<MapBoxContextType['loading']>(true);
  const [initialFloorsDataIsLoaded, setInitialFloorsDataIsLoaded] = useState<MapBoxContextType['initialFloorsDataIsLoaded']>(false);
  const [mapConfig, setMapConfig] = useState<MapBoxContextType['mapConfig']>({ ...mapConfigDefaultVars, ...initialData.config });
  const [mapApiResponse, setMapApiResponse] = useState<MapBoxContextType['mapApiResponse']>({
    retailers: [],
    globalRetailerIds: [],
    floors: [],
    kiosks: [],
    map_objs: [],
    mapSettings: defaultPortalMapSettings
  });

  if (mapApiResponseRef) {
    mapApiResponseRef.current = mapApiResponse;
  }

  // angrej remove this later
  const [appData, setAppData] = useState<MapBoxContextType['appData']>({
    AMENITIES: AllAmenities,
    FLOORS: [],
    KIOSKS: {},
    RETAILERS: {}
  });

  const [meshByObjectId, setMeshByObjectId] = useState<MapBoxContextType['meshByObjectId']>(new Map<string, Mesh>());

  const [allMapObjects, setAllMapObjects] = useState<MapBoxContextType['allMapObjects']>([]);
  const [allIndexedMapObjects, setAllIndexedMapObjects] = useState<MapBoxContextType['allIndexedMapObjects']>({});
  const [allIndexedRetailers, setAllIndexedRetailers] = useState<MapBoxContextType['allIndexedRetailers']>({});
  const [indexedKiosks, setIndexedKiosks] = useState<MapBoxContextType['indexedKiosks']>({});
  const [floorsData, setFloorsData] = useState<MapBoxContextType['floorsData']>([]);

  const [allNodesFloor, setAllNodesFloor] = useState<MapBoxContextType['allNodesFloor']>({});

  const [pathFinderGraph, setPathFinderGraph] = useState<MapBoxContextType['pathFinderGraph']>(createGraph());
  const [ngraphPath, setNgraphPath] = useState<MapBoxContextType['ngraphPath']>(path);

  const [meshObject, setMeshObject] = useState<MapBoxContextType['meshObject']>([]);
  const [cameraLength, setCameraLength] = useState<number | undefined>();

  const apiServices: ApiServicesProps = useMemo(() => {
    return createApiServices(apiBaseUrl, mapConfig.CENTER_ID);
  }, [apiBaseUrl, mapConfig.CENTER_ID])

  // setting mapConfig when initialData.config is changed (for example, when CENTER_ID is changed)
  // will trigger a re-fetch of the map data
  useEffect(() => {
    setMapConfig((config) => {
      const newConfig = { ...config, ...initialData.config };
      return newConfig;
    });
  }, [initialData.config, initialData.config.CENTER_ID]);

  const refetchMapData = useCallback(() => {
    Promise.all([
      apiServices.retailersService.getAllRetailers(),
      apiServices.mapApiService.getMapData(),
      apiServices.mapApiService.getMapFloors(),
      apiServices.mapApiService.getMapKiosks(),
      apiServices.mapApiService.getMapSettings(),
      apiServices.mapApiService.getMapResponsiveSettings()
    ])
      .then(([
        { retailers, globalRetailerIds },
        mapData,
        mapFloors,
        mapKiosks,
        mapSettings,
        responsiveSettings
      ]) => {
        // find all mapData.map_obj that use the same map_obj_name as another map_obj
        // can be removed once the db don't have duplicates
        const mapObjNames = mapData.map(map_obj => map_obj.map_obj_name);
        mapData = mapData.filter((map_obj, index) => mapObjNames.indexOf(map_obj.map_obj_name) === index);

        setMapApiResponse({
          retailers,
          globalRetailerIds,
          map_objs: mapData,
          floors: mapFloors,
          kiosks: mapKiosks,
          mapSettings: mapSettings.apiResponse
        });

        setMapConfig((config) => {
          const mergedMapConfig = {
            ...config,
            ...mapSettings.responseConfig,
            CAMERA_CONTROLS_STATES: responsiveSettings,
            CENTER_ID: initialData.config.CENTER_ID || config.CENTER_ID,
            KIOSK_ID: initialData.config.KIOSK_ID || config.KIOSK_ID,
            SELECTED_RETAILER_ID: initialData.config.SELECTED_RETAILER_ID || config.SELECTED_RETAILER_ID,
          };
          return mergedMapConfig;
        });

        setAllIndexedRetailers(retailers.reduce(
          (acc, retailer) => {
            acc[retailer.id] = retailer;
            return acc;
          },
          {} as Record<number, MapRetailer>
        ));

        setIndexedKiosks(mapKiosks.reduce(
          (acc, kiosk) => {
            acc[kiosk.id] = kiosk;
            const mapObj = mapData.find(item => item.kiosk_id === kiosk.id);
            if (mapObj) {
              acc[kiosk.id].map_obj_name = mapObj.map_obj_name;
            }
            return acc;
          },
          {} as Record<number, MapKiosk>
        ));

        setAllIndexedMapObjects(mapData.reduce(
          (acc, mapObj) => {
            acc[mapObj.map_obj_name] = { ...mapObjDefaultVars, ...mapObj };
            return acc;
          },
          {} as Record<string, MapObjData>
        ));

        setFloorsData(mapFloors.map(floor => {
          const objsGroup = new Group();
          objsGroup.scale.y *= -1;
          objsGroup.lookAt(0, 1, 0);

          return {
            ...floor,
            objsGroup: objsGroup,
            interactiveObjs: [],
            escalatorsObjs: [],
            escalatorsNodes: {},
            escalatorMeshes: [],
            route_points: [],
            route_tube: undefined,
            route_texture: null,
            routeMeshes: [],
            route_active: false,
          };
        }));
        setInitialFloorsDataIsLoaded(true);

      })
      .catch(error => {
        console.error('Error: MapBoxContextProvider fetching api data:', error);
        // there might still be some data in the context, so we can still use it
        setInitialFloorsDataIsLoaded(true);
      });
  }, [apiServices])

  useEffect(() => {
    setInitialFloorsDataIsLoaded(false);
    refetchMapData();
  }, [refetchMapData]);

  const contextValue: MapBoxContextType = {
    apiBaseUrl,
    refetchMapData,
    apiServices,

    loading,
    setLoading,
    initialFloorsDataIsLoaded,
    setInitialFloorsDataIsLoaded,
    mapConfig,
    setMapConfig,
    mapApiResponse,
    setMapApiResponse,
    appData,
    setAppData,

    meshByObjectId,
    setMeshByObjectId,
    allMapObjects,
    setAllMapObjects,
    allIndexedMapObjects,
    setAllIndexedMapObjects,
    allIndexedRetailers,
    setAllIndexedRetailers,
    indexedKiosks,
    setIndexedKiosks,
    floorsData,
    setFloorsData,

    allNodesFloor,
    setAllNodesFloor,
    pathFinderGraph,
    setPathFinderGraph,
    ngraphPath,
    setNgraphPath,

    meshObject,
    setMeshObject,
    cameraLength,
    setCameraLength
  };

  return (
    <MapBoxContext.Provider value={contextValue}>
      {children}
    </MapBoxContext.Provider>
  )
}

export function useMapBoxContext() {
  const context = useContext(MapBoxContext);

  if (context === undefined) {
    throw new Error("useMapBoxContext must be used within MapBoxContextProvider");
  }

  return context;
}

export default MapBoxContextProvider;
