import { SVGResult } from "three-stdlib";
import { IMeshValues } from "src/interfaces";
import { Raycaster } from "three";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import useRouteHelper from "./useRouteHelper";
import useMaterialAndGeometry from "./useMaterialAndGeometry";
import { useMeshParams } from "./useMeshParams";
import { useMemo } from "react";

// TODO: get rid of this global variable
let node_count = 0;

const useLoadFloors = (svgFloors: SVGResult[]): any => {
  const { mapConfig, pathFinderGraph } = useMapBoxContext();
  const { getMaterialAndGeometry } = useMaterialAndGeometry();
  const { getMeshParams } = useMeshParams();
  const { assign_route_nodes_to_stores, linkFloorEscalators } = useRouteHelper();

  const [GeometriesAndMaterials, escalator_nodes, allSvgObjectIds] = useMemo(() => {

    const allSvgObjectIds: Set<string> = new Set();
    const GeometriesAndMaterials: Array<IMeshValues[]> = [];
    const escalator_nodes: string[] = [];

    svgFloors.forEach((svgFloor, floor_index) => {
      if (svgFloor.paths && svgFloor.paths.length) {
        const pathIds = [];
        for (let i = 0; i < svgFloor.paths.length; i++) {
          pathIds.push(svgFloor.paths[i]?.userData?.node.id);
        }
        const paths = svgFloor.paths;
        for (let i = 0; i < paths.length; i++) {
          const {
            mesh_type,
            layer_color,
            extrude,
            z_index,
            mesh_visible,
            mesh_draw,
            mesh_transparent,
            line_thickness,
            path,
            layer_name,
          } = getMeshParams(
            paths[i],
            paths,
            floor_index,
            ++node_count,
          );

          if (mesh_draw) {
            const meshParams = getMaterialAndGeometry(
              mesh_type,
              layer_name,
              layer_color,
              mesh_transparent,
              mesh_visible,
              z_index,
              extrude,
              line_thickness,
              floor_index,
              path
            );

            if (layer_name.startsWith('kiosk-')) {
              if (mapConfig.ROLE === 'PORTAL') {
                // meshParams.mesh.visible = false;
                //@ts-ignore
                // meshParams.mesh.material.visible = false;
              }
            }

            if (!GeometriesAndMaterials[floor_index]) {
              GeometriesAndMaterials[floor_index] = [];
            }

            GeometriesAndMaterials[floor_index].push(meshParams);
          }
        }
        const findStoreNodeRaycaster = new Raycaster();
        const routeNodeIds = assign_route_nodes_to_stores(
          floor_index,
          escalator_nodes,
          findStoreNodeRaycaster
        );

        if (routeNodeIds.size && GeometriesAndMaterials[floor_index].length) {
          for (let i = 0; i < GeometriesAndMaterials[floor_index].length; i++) {
            if (routeNodeIds.has(GeometriesAndMaterials[floor_index][i].mesh.object_id)) {
              GeometriesAndMaterials[floor_index][i].mesh.route_node_id = routeNodeIds.get(GeometriesAndMaterials[floor_index][i].mesh.object_id);
            }
          }
        }

        linkFloorEscalators();
      }
    })

    for (let i = 0; i < GeometriesAndMaterials.length; i++) {
      const floorGeometries = GeometriesAndMaterials[i];
      for (let j = 0; j < floorGeometries.length; j++) {
        allSvgObjectIds.add(floorGeometries[j].object_id);
      }
    }

    return [GeometriesAndMaterials, escalator_nodes, allSvgObjectIds]

  }, [assign_route_nodes_to_stores, getMaterialAndGeometry, getMeshParams, linkFloorEscalators, mapConfig.ROLE, svgFloors]);
  // }, [assign_route_nodes_to_stores, getMaterialAndGeometry, getMeshParams, linkFloorEscalators, mapConfig.ROLE, svgFloors]);

  return {
    GeometriesAndMaterials,
    graph: pathFinderGraph,
    escalator_nodes,
    allSvgObjectIds
  }
}

export default useLoadFloors;