import { IExtMesh, MeshFloorsProps, IMeshValues } from "../../interfaces";
import { ThreeEvent } from "@react-three/fiber";
import React, { useEffect } from "react";
import { Mesh, MeshLambertMaterial, MeshStandardMaterial, RGB } from "three";
import { RouteTube } from "./RouteTube";
import { Html } from "@react-three/drei";
import styles from './Map.module.scss';
import { MapCenterMarker } from "./MapCenterMarker";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";
import { getSvgIconHTML } from "src/helpers/misc.helper";

interface IMapProps {
  floorIndex: number,
  currentFloorIndex: number;
  meshFloors: MeshFloorsProps;
  currKioskObj: Mesh | null;
  activeObjectId?: string;
  hoverObjectId?: string;
  routeTube?: Mesh;
  visible: boolean,
  onPointerEnter?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerLeave?: (e: ThreeEvent<PointerEvent>) => void;
  onPointerMove?: (e: ThreeEvent<PointerEvent>) => void;
  onClick?: (e: ThreeEvent<MouseEvent>) => void;
  handleChangeFloor: (index: number) => void;
}

const getDarkerColor = (color: RGB) => {
  if (color) {
    var newColor = { ...color }
    newColor.r = color.r * 0.92;
    newColor.g = color.g * 0.92;
    newColor.b = color.b * 0.92;
    return newColor;
  }
  return null;
}

interface MapObjectProps {
  type: string;
  mesh: IExtMesh;
  visible: boolean;
  currentFloorIndex: number;
  params?: IMeshValues;
}

const MapObject = (props: MapObjectProps) => {
  const { type, mesh, currentFloorIndex } = props;
  useEffect(() => {
    mesh.userData.triggerUpdate = () => {
      // setUpdate((value) => !value);
    };
    return () => mesh.userData.triggerUpdate = undefined
  }, [mesh.userData]);

  const { cameraLength } = useMapBoxContext();

  switch (type) {
    case 'storeLogo':
      if (mesh.userData.kioskConfig && mesh.userData.kioskConfig.floorIndex === currentFloorIndex) {
        return (
          <primitive key={mesh.uuid} object={mesh}>
            <MapCenterMarker size={mesh.userData.kioskConfig.size} blink={mesh.userData.kioskConfig.blink} cameraLength={cameraLength} lockSize={mesh.userData.kioskConfig.lockSize} text_color={mesh.userData.kioskConfig.text_color} />
          </primitive>
        )
      } else {
        return (
          <primitive key={mesh.uuid} object={mesh}>
            {mesh.userData.htmlContent ? <Html style={{ pointerEvents: 'none' }} distanceFactor={mesh.userData.distanceFactor}>{mesh.userData.htmlContent}</Html> : null}
          </primitive>
        )
      }
    case 'kiosk':
      if (mesh.userData.kioskConfig && mesh.userData.kioskConfig.floorIndex === currentFloorIndex) {
        return (
          <MapCenterMarker size={mesh.userData.kioskConfig.size} blink={mesh.userData.kioskConfig.blink} cameraLength={cameraLength} lockSize={mesh.userData.kioskConfig.lockSize} text_color={mesh.userData.kioskConfig.text_color} />
        )
      } else {
        return null;
      }
    default:
      return null;
  }
}

export const Map = (params: IMapProps) => {
  const { meshFloors, routeTube, floorIndex, currentFloorIndex, activeObjectId, hoverObjectId, visible, handleChangeFloor } = params;
  const { mapConfig, floorsData } = useMapBoxContext();

  const { meshParams, textParams, storeLogos } = meshFloors;
  const floor = floorsData[floorIndex];

  const onPointerMove = params.onPointerMove;
  const onPointerOver = params.onPointerEnter;
  const onPointerOut = params.onPointerLeave;
  const onClick = params.onClick;

  const floorMeshParams = meshParams[floorIndex];
  const floorTextParams = textParams[floorIndex];
  const floorStoreLogos = storeLogos[floorIndex];
  const escalatorMeshes = floorsData[floorIndex].escalatorMeshes;

  return (
    <group visible={visible}>
      {floorMeshParams?.map((params) => {
        if (!params.mesh) return null;

        const interactive = (visible && params.mesh.visible && floor.interactiveObjs && floor.interactiveObjs.includes(params.mesh));
        const hovered = hoverObjectId && params.mesh.object_id === hoverObjectId;
        const active = activeObjectId && params.mesh.object_id === activeObjectId;
        if (interactive) {
          // TODO: move those colorDefault, active, etc to userData of mesh or material
          if ((active || hovered) && (params.mesh.material instanceof MeshLambertMaterial || params.mesh.material instanceof MeshStandardMaterial)) {
            //@ts-ignore
            const baseColor = active ? mapConfig.ACCENT_COLOR : params.mesh.material.colorDefault;
            params.mesh.material.color = hovered ? getDarkerColor(baseColor) : baseColor;
          } else {
            // @ts-ignore
            params.mesh.material.color = params.mesh.material.colorDefault;
          }
        }

        return <group key={params.mesh.uuid} {...(interactive ? { onPointerOver, onPointerMove, onPointerOut, onClick } : {})}>
          <primitive object={params.mesh}></primitive>
        </group>
      })}
      {floorTextParams?.map((params) => {
        if (!params.textMesh) return null;
        return (
          <React.Fragment key={params.textMesh.uuid}>
            <primitive object={params.textMesh}>
              {params.textMesh.userData.kioskConfig ? <MapObject key={undefined} mesh={params.textMesh} currentFloorIndex={currentFloorIndex} type={'kiosk'} visible={visible} /> : null}
            </primitive>
          </React.Fragment>
        )
      })}

      {floorStoreLogos?.map((params) => {
        if (!params.storeLogo) return null;
        return <MapObject key={params.storeLogo.uuid} mesh={params.storeLogo} currentFloorIndex={currentFloorIndex} type={'storeLogo'} visible={visible} />
      })}

      {escalatorMeshes?.map((params) => {
        return <primitive key={`escalator-${params.object_id}`} object={params}>
          <Html visible={true} position={params.geometry.boundingSphere?.center}>
            <div className={`escalator_elems ${styles.escalator_elems}`}
              style={{ display: visible ? 'block' : 'none' }}
              onClick={(e) => {
                e.stopPropagation()
                handleChangeFloor(params.goToFloor?.index || 0)
                return false
              }}
              onPointerMove={(e) => e.stopPropagation()}>
              <div className={`element ${styles.element}`} id={params.object_id as string}>
                <div className={`svgIcon ${styles.svgIcon}`} dangerouslySetInnerHTML={{ __html: getSvgIconHTML('escalator') as string }}></div>
                <div className={`label ${styles.label}`}>{params.goToFloor?.direction}: {params.goToFloor ? floorsData[params.goToFloor.index].name : ''}</div>
              </div>
            </div>
          </Html>
        </primitive>
      })}

      <RouteTube mesh={routeTube} />
    </group>
  )
}