import { MouseEventHandler, useEffect, useMemo } from "react";
import styles from './UIComponent.module.scss';
import { MapAmenity, MapAmenityID } from "src/helpers/amenities.helper";
import { getSvgIconHTML } from "src/helpers/misc.helper";
import { useMapBoxContext } from "src/contexts/MapBoxContextProvider";

export interface UIComponentProps {
  selectedFloorIndex: number;
  handleFloorChange: (floorIndex: number) => void;
  amenitiesList: MapAmenity[],
  zoomIn?: MouseEventHandler<HTMLDivElement>,
  zoomOut?: MouseEventHandler<HTMLDivElement>,
  reset?: (fullReset?: boolean) => void,
  clearRoute?: MouseEventHandler<HTMLDivElement>,
  handleAmenityClick: (type: string, map_obj_name?: string) => void,
  onExtractedAmenities?: (amenityIds: MapAmenityID[]) => void
}

const UIComponent = (params: UIComponentProps) => {
  const { selectedFloorIndex, handleFloorChange, amenitiesList, zoomIn, zoomOut, reset, handleAmenityClick, onExtractedAmenities } = params;
  const { mapConfig, mapApiResponse, floorsData, allMapObjects } = useMapBoxContext();

  const handleResetClick: MouseEventHandler = (e) => {
    e.preventDefault();
    if (reset instanceof Function) {
      if (mapConfig.ROLE === 'PORTAL_RESPONSIVE' || mapConfig.ROLE === 'PORTAL') {
        reset(true)
      } else {
        reset();
      }
    }
  }

  const extractValues = useMemo<MapAmenityID[]>(() => {
    if (mapApiResponse?.map_objs) {
      return mapApiResponse?.map_objs
        .filter(obj => obj.layer_type === "amenity" && allMapObjects.includes(obj.map_obj_name))
        .map(obj => obj.value as MapAmenityID);
    }
    return [];
  }, [mapApiResponse, allMapObjects])


  useEffect(() => {
    if (onExtractedAmenities) {
      onExtractedAmenities(extractValues)
    }
  }, [onExtractedAmenities, extractValues])

  return (
    <div id={styles['threejs-map-wrapper']}>
      <div id={styles['threejs-map']}>
        {floorsData.length > 0 && (
          <div className={`map_controls ${styles.map_controls}`}>
            <div className="map_controls_wrapper">
              {floorsData.length > 1 && (
                <div className="floors btn_group">
                  {floorsData.map((floor: any, index: number) => (
                    <div key={index} className={`floor control_btn ${index === selectedFloorIndex ? 'active' : ''}`} onClick={() => {
                      handleFloorChange(index)
                    }}>{floor.name}</div>
                  ))}
                </div>
              )}

              <div className="reset-zoom">
                <div onClick={handleResetClick} className="reset_btn control_btn">RESET</div>

                <div className="zoom_btns">
                  <div onClick={zoomOut} className="zoom-out control_btn"></div>
                  <div onClick={zoomIn} className="zoom-in control_btn">+</div>
                </div>
              </div>
            </div>
          </div>
        )}

        <div className={`loading_map ${styles['hide']}`}>Loading map, please wait...</div>

        {!['PORTAL', 'DISPLAY_APP'].includes(mapConfig.ROLE) &&
          <div className={`amenities ${styles.amenities}`} style={{ display: 'block' }}>
            {amenitiesList.map((amenity, index) => {
              if (!extractValues.includes(amenity.type)) {
                return null;
              }
              return (
                <div key={`amenity-${amenity.name}-${index}`}
                  className={`amenity ${styles.amenity}`}
                  onClick={() => handleAmenityClick(amenity.type)}>
                  <div className={`content ${styles.content}`}>
                    <div className={`icon ${styles.icon}`} dangerouslySetInnerHTML={{ __html: getSvgIconHTML(amenity.type) as string }}></div>
                    <div className={`name ${styles.name}`}>{amenity.name}</div>
                  </div>
                </div>
              )
            })}
          </div>
        }
      </div>
    </div>
  )
}

export default UIComponent;
