import { AxiosInstance } from "axios";
import { defaultPortalMapSettings, mapConfigDefaultVars } from "src/defaults";
import { hex_to_color } from "src/helpers/misc.helper";
import { MapConfigProps } from "src/interfaces";
import { MapResponsiveSettings, MapObjData, MapFloor, MapKiosk } from "src/interfaces/mapApiTypes";
import { PortalMapSettingsProps } from "src/interfaces/portal";

export interface MapApiService {
  getMapData: () => Promise<MapObjData[]>;
  getMapFloors: () => Promise<MapFloor[]>;
  getMapKiosks: () => Promise<MapKiosk[]>;
  getMapSettings: () => Promise<{ apiResponse: any, responseConfig: Partial<MapConfigProps> }>;
  getMapResponsiveSettings: () => Promise<MapResponsiveSettings>;
}

const createMapApiService = (axiosInstance: AxiosInstance): MapApiService => {
  const CENTER_ID = axiosInstance.defaults.headers['center_id'];

  const getMapData = (): Promise<MapObjData[]> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/mapit2/data`)
        .then((response: any) => {
          const mapDataItems: MapObjData[] = [];
          if (response?.data?.items) {
            for (const item of response?.data?.items) {
              mapDataItems.push({
                id: item.id,
                retailer_id: item.retailer_id,
                kiosk_id: item.kiosk_id,
                map_obj_name: item.map_obj_name,
                obj_type: item.obj_type,
                layer_type: item.layer_type,
                value: item.value,
                custom_text: item.custom_text,
                custom_image: item.custom_image,
                bg_color: item.bg_color,
                transparent: item.transparent,
                text_color: item.text_color,
                size: parseInt(item.size),
                rotate: parseInt(item.rotate),
                offset_x: parseInt(item.offset_x),
                offset_y: parseInt(item.offset_y),
                lock_size: false,
              })
            }
          }
          resolve(mapDataItems);
        })
        .catch((err) => {
          console.error('Error mapApiService:getMapData()', err);
          resolve([]);
        });
    });
  }

  const getMapFloors = (): Promise<MapFloor[]> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/mapit2/floors?limit=100&offset=0`)
        .then((response: any) => {
          const floors: MapFloor[] = [];
          if (response?.data?.items) {
            for (const item of response?.data?.items) {
              floors.push({
                id: item.id,
                name: item.title,
                svgUrl: item.svg
              })
            }
          }
          resolve(floors);
        })
        .catch((err) => {
          console.error('Error mapApiService:getMapFloors()', err);
          resolve([]);
        });
    });
  }

  const getMapKiosks = (): Promise<MapKiosk[]> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/display_kiosks?limit=100&page=1&display_type_id=1`)
        .then((response: any) => {
          const kiosks: MapKiosk[] = [];
          if (response?.data?.items) {
            for (const item of response?.data?.items) {
              kiosks.push({
                id: item.id,
                name: item.title,
              })
            }
          }
          resolve(kiosks);
        })
        .catch((err) => {
          console.error('Error mapApiService:getMapKiosks()', err);
          resolve([]);
        });
    });
  }

  const getMapSettings = (): Promise<{ apiResponse: any, responseConfig: Partial<MapConfigProps> }> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/mapit2/settings/${CENTER_ID}`)
        .then((response: any) => {
          const settings: MapConfigProps = { ...mapConfigDefaultVars };
          let dbSettings: Partial<PortalMapSettingsProps> = defaultPortalMapSettings;
          if (response?.data?.settings) {
            dbSettings = response?.data?.settings;
            if (dbSettings['MAP_BACKGROUND_COLOR']) settings.MAP_BACKGROUND_COLOR = hex_to_color(dbSettings['MAP_BACKGROUND_COLOR']);
            if (dbSettings['STORE_DEFAULT_COLOR']) settings.STORE_DEFAULT_COLOR = hex_to_color(dbSettings['STORE_DEFAULT_COLOR']);
            if (dbSettings['ACCENT_COLOR']) settings.ACCENT_COLOR = hex_to_color(dbSettings['ACCENT_COLOR']);
            if (dbSettings['BASE_COLOR']) settings.BASE_COLOR = hex_to_color(dbSettings['BASE_COLOR']);
            if (dbSettings['BOUNDARY_COLOR']) settings.BOUNDARY_COLOR = hex_to_color(dbSettings['BOUNDARY_COLOR']);
            if (dbSettings['BASE_COLOR']) settings.BASE_COLOR = hex_to_color(dbSettings['BASE_COLOR']);
            if (dbSettings['WALL_COLOR']) settings.WALL_COLOR = hex_to_color(dbSettings['WALL_COLOR']);
            if (dbSettings['STORE_TEXT_COLOR']) settings.STORE_TEXT_COLOR = hex_to_color(dbSettings['STORE_TEXT_COLOR']);
            if (dbSettings['OVERLAY_COLOR']) settings.OVERLAY_COLOR = hex_to_color(dbSettings['OVERLAY_COLOR']);
            if (dbSettings['BOUNDARY_THICKNESS']) settings.BOUNDARY_THICKNESS = dbSettings['BOUNDARY_THICKNESS'];
            if (dbSettings['WALL_THICKNESS']) settings.WALL_THICKNESS = dbSettings['WALL_THICKNESS'];
            if (dbSettings['OVERLAY_OPACITY']) settings.OVERLAY_OPACITY = dbSettings['OVERLAY_OPACITY'];
            if (dbSettings['KIOSK_SIZE']) settings.KIOSK_SIZE = dbSettings['KIOSK_SIZE'];
            if (dbSettings['ZOOM_STEP']) settings.ZOOM_STEP = dbSettings['ZOOM_STEP'];
            if (dbSettings['SELECTED_ZOOM_LIMIT']) settings.SELECTED_ZOOM_LIMIT = dbSettings['SELECTED_ZOOM_LIMIT'];


            settings.CAMERA.minDistance = dbSettings['CAMERA_MIN_DISTANCE'] ? dbSettings['CAMERA_MIN_DISTANCE'] : settings.CAMERA.minDistance
            settings.CAMERA.maxDistance = dbSettings['CAMERA_MAX_DISTANCE'] ? dbSettings['CAMERA_MAX_DISTANCE'] : settings.CAMERA.maxDistance

            settings.CAMERA = { ...settings.CAMERA } // new reference to trigger some useEffects
          }

          const settingsToReturn: Partial<MapConfigProps> = { ...settings }

          delete settingsToReturn.DEVICE
          delete settingsToReturn.ROLE
          resolve({
            apiResponse: dbSettings,
            responseConfig: settingsToReturn,
          });
        })
        .catch((err) => {
          console.error('Error mapApiService:getMapSettings()', err);
          resolve({
            apiResponse: defaultPortalMapSettings,
            responseConfig: mapConfigDefaultVars,
          });
        });
    });
  }

  const getMapResponsiveSettings = (): Promise<MapResponsiveSettings> => {
    return new Promise((resolve, reject) => {
      axiosInstance(`/v1/mapit2/responsive/${CENTER_ID}`)
        .then((response: any) => {
          const settings: MapResponsiveSettings = { ...mapConfigDefaultVars.CAMERA_CONTROLS_STATES };
          if (response.data) {
            if (response.data.display_app) settings.display_app = response.data.display_app;
            if (response.data.desktop) settings.desktop = response.data.desktop;
            if (response.data.tablet) settings.tablet = response.data.tablet;
            if (response.data.mobile) settings.mobile = response.data.mobile;
          }
          resolve(settings);
        })
        .catch((err) => {
          console.error('Error mapApiService:getMapResponsiveSettings()', err);
          resolve({ ...mapConfigDefaultVars.CAMERA_CONTROLS_STATES });
        });
    });
  }

  return {
    getMapData,
    getMapFloors,
    getMapKiosks,
    getMapSettings,
    getMapResponsiveSettings
  }
}

export default createMapApiService;

